  imClicked(e){

    let id:string=$(e.srcElement).parent().attr('id');


    if(id=='acn_UserGuide'){

      $('#navToggler').trigger('click');

      let openNewWin:boolean = (this.UGWin==undefined);
      if(!openNewWin)openNewWin = this.UGWin.closed;

      if(openNewWin)this.UGWin=window.open('assets/docs/user_guide.pdf','_blank');

      try{
        this.UGWin.focus();
      }catch(e){
        alert(e.message);
      }


      return;
    }

    this.udf.searchText='';

    $(e.srcElement.parentNode.parentNode.parentNode).removeClass('show');

    let module:string=id.split('_')[1];

    window.localStorage.setItem("currentModule", module);

    if(
       module=='Calendar' ||
       module=='CalendarActivity' ||
       module=='CalendarActivityMTIAP' ||
       module=='POBProfile'
      ){

      this.udf.currentModule=module;
      this.displayDataComponents=false;

    }else{

      this.oplandb.onActivityLoadAction=true;


      if(module=='Personnel'){
        this.udf.currentModule='By Name';
        this.oplandb.setPersonnelNavGroup();
      }else{
        this.udf.currentModule=module;
        this.oplandb.setOtherNavGroup();
      }

      this.displayDataComponents=true;

    }

  }

