  getMonthRange(per:number):string{
    let ret:string="";
    let spy:number=this.udf.calActivityMTIAPPagePerYear;
    let y2:boolean=(per>spy);
    let mos:number=12 / spy;
    let yr:number = Number(this.udf.calActivityMTIAPYear) + (y2 ? 1 : 0);

    let mo = (per-1-(y2?spy:0)) * mos

    if(mos==1){
      ret = G.monthNames[mo].substr(0,3) +' ' + yr;
    }else{
      ret = G.monthNames[mo].substr(0,3) +" - " +G.monthNames[mo+mos-1].substr(0,3) +' ' + yr;
    }

    return ret;
  }


