// returns the complete url part of the request to be made by the data service of a component
export function getUrlRequest(service:string,method:string,params?:string) {
    let loc:string=$(location).attr('href').substr(0,16).toLowerCase();
    let url:string=(loc =='http://localhost' ?
      datSourceUrl : '') + service + '.asmx/'+ method + (params==undefined?'':'?'+params);

      console.log("data url:"+ url);
    return url;
}

export function pad0(num:number,chrs?:number):string{
  // up to 50 characters
  let retVal:string=("0".repeat(50) + String(num));
  retVal = retVal.substr(retVal.length-chrs);
  return retVal;
}

export function padRS(str:string,chrs?:number):string{
  // up to 255 characters
  if(chrs==undefined)chrs=255;
  let retVal:string=(str + " ".repeat(255));
  retVal = retVal.substr(0, chrs);
  return retVal;
}

export function epochToDate(epochDate:string,retLong?:boolean):any{
  if(retLong==undefined)retLong=false;
  let dt:Date = new Date(parseFloat(epochDate.substr(6)));
  if(retLong){
    return this.dateToString(dt,true);
  }else{
    return dt;
  }
}



export const monthNames:Array<string> = [
  "January", "February", "March",
  "April","May","June",
  "July","August","September",
  "October","November","December"
]

export const monthIndex:object = {
    "jan":0, "feb":1, "mar":2,
    "apr":3,"may":4,"jun":5,
    "jul":6,"aug":7,"sep":8,
    "oct":9,"nov":10,"dec":11
};


export function getMonthName(monthIndex:number, short?:boolean):string{
  if(short==undefined)short=false;
  return short ? monthNames[monthIndex].substr(0,3) :  monthNames[monthIndex];
}

export const daysOfWeek:Array<string> = [
  "Sunday","Monday","Tuesday","Wednesday",
  "Thursday","Friday","Saturday"
]

export function getDayOfWeek(dayIndex:number, short?:boolean):string{
  if(short==undefined)short=false;
  return short ? daysOfWeek[dayIndex].substr(0,3) :  daysOfWeek[dayIndex];
}

export function stringToDate(strrDate:string):Date{
  if(strrDate==undefined){
    return new Date();
  }else{
    return new Date(parseInt(strrDate.substr(0,4)),parseInt(strrDate.substr(4,2))-1,parseInt(strrDate.substr(6,2)));
  }

}

export function setFocus(ctlID:string){
  setTimeout(()=>{$('#'+ctlID).focus()},10);
}

export function dateToString(dateObj:Date,retLong?:boolean ):string{
  if(retLong==undefined)retLong=false;
  let mo:string=String(dateObj.getMonth()+1)
  let day:string=String(dateObj.getDate())

  if(retLong){

    return (day.length==1 ? "0" : "") + day + ' ' +
      String(monthNames[dateObj.getMonth()]).substr(0,3) + ' ' +
      String(dateObj.getFullYear());

  }else{
    return String(dateObj.getFullYear()) +
      (mo.length==1 ? "0" : "") + mo +
      (day.length==1 ? "0" : "") + day;
  }

}

export function monthDiff(d1, d2) {
  var months;
  months = (d2.getFullYear() - d1.getFullYear()) * 12;
  months -= d1.getMonth() + 1;
  months += d2.getMonth();
  return months <= 0 ? 0 : months;
}

export function daysDiff(d1:Date,d2:Date):number{
  let diff:number = Math.abs(d1.getTime() - d2.getTime());
  return Math.ceil(diff / (1000 * 3600 * 24));
}

export function addDays(date: Date, days: number): Date {
  let newDate:Date=new Date(date.getFullYear(),date.getMonth(),date.getDate());
  newDate.setDate(newDate.getDate() + parseInt(String(days)));
  return newDate;
}

export function addDaysVal(date: Date, days: number): Date {
  let dt:Date=new Date(date.getFullYear(),date.getMonth(),date.getDay());
  dt.setDate(date.getDate() + parseInt(String(days)));
  return dt;
}

export function shortToLongDate(yyyymmdd:string):string{
  if(yyyymmdd.length==0)return "";
  let dt:Date = stringToDate(yyyymmdd);
  return dateToString(dt,true);
}

export function longToShortDate(ddmmmyyyy:string):string{
  if(ddmmmyyyy.length==0)return "";
  let dArr:Array<string>=ddmmmyyyy.split(" ");

  if(dArr.length!=3)dArr=ddmmmyyyy.split("-");    // if date is hyphen(-) delimited

  let mo:string = "0" + (monthIndex[dArr[1].toLowerCase()]+1);

  if(dArr[2].length==2) dArr[2]="20"+dArr[2]; // if year specitied is not 4 digits
  if(dArr[0].length==1) dArr[0]="0" + dArr[0]; // if year specitied is not 4 digits

  return dArr[2]+mo.substr(mo.length-2,2)+dArr[0];
}

export function longToDate(ddmmmyyyy:string):Date{

  try{
    let strDate:string=this.longToShortDate(ddmmmyyyy) + "";
    if(strDate.length==0){
      return null;
    }else{
      return this.stringToDate(strDate);
    }
  }catch(e){
    return null;
  }

}

