export function groupBy(arr, key, objLookup?,lookupKey?:string,lookupFields?:Array<string>,sortField?:string):Array<object> {
  var newArr = [],
      types = {},
      newItem, i, j, cur;
  for (i = 0, j = arr.length; i < j; i++) {
      cur = arr[i];
      if (!(cur[key] in types)) {
          types[cur[key]] = { type: cur[key], data: [] };
          newArr.push(types[cur[key]]);
      }
      types[cur[key]].data.push(cur);
  }

  if(objLookup!=undefined){
    if(newArr){
      newArr.forEach( item => {
          let obj =  objLookup.find(lkpItem => item['type'] == lkpItem[lookupKey ? lookupKey : 'id']);
          if(obj){
            item['name'] = obj['name']!=undefined ? obj['name'] : "unknown";
          }else{
            item['name'] = "unknown";
          }
          if(lookupFields!=undefined){
            // attach all fields from the lookup item to the current item of the input array
            lookupFields.forEach(field => {

              if(obj){
                item[field] = obj[field];
              }else{
                item[field] = null;
              }

            });
          }
        }
       )
    }
    if(sortField!=undefined && sortBy!=null){
      this.sortBy(newArr,sortField)
    }else{
      this.sortBy(newArr,'name');
    }

  }else{
    newArr.forEach( item => { item['name'] = item['type'] });
  }

  return newArr;
}

export function sortBy(arr:Array<any>, key:string,desc?:boolean){


  if(arr==undefined)return;

  if(desc==undefined)desc=false;

  if(desc){

    arr.sort((a,b) => {
      return (a[key] < b[key]) ? 1 : ((b[key] < a[key]) ? -1 : 0);
    })

  }else{
    arr.sort((a,b) => {
      return (a[key] > b[key]) ? 1 : ((b[key] > a[key]) ? -1 : 0);
    })
  }

}


@Injectable()
export class AppDataService {

  constructor(public http:Http) {

  }

  getAppData(requestUrl:string,body?:any,options?:RequestOptionsArgs):Observable<Array<object>>{
    let varBody:any="";
    let varOptions:RequestOptionsArgs=null;

    if(body!=undefined)varBody=body;
    if(options!=undefined)varOptions=options;

    return this.http
      .post(requestUrl,varBody,varOptions)
      .map((response:Response) => <Array<object>>response.json());
  }

  getAppDataWS(service:string,method:string,params?:string):Observable<Array<object>>{
      let url:string;
      if(params!=undefined){
        url=getUrlRequest(service,method) + '?'+ params;
      }else{
        url=getUrlRequest(service,method)
      }

//      alert(url);


      return this.getAppData(url);
  }

  saveAppDataWS(service:string,method:string,body?:any,options?:RequestOptionsArgs):Observable<Array<object>>{

    let url:string;
    let varBody:any="";
    let varOptions:RequestOptionsArgs=undefined;

    if(body!=undefined)varBody=body;
    if(options!=undefined)varOptions=options;

    url=getUrlRequest(service,method)

    return this.getAppData(url,varBody,varOptions);
  }

}

export class NavService {
  navchange: EventEmitter<object> = new EventEmitter();
  constructor() {}
  emitNavChangeEvent(e:object) {
    this.navchange.emit(e);
  }
  getNavChangeEmitter() {
    return this.navchange;
  }

}




