import { Component, OnInit, Inject, ElementRef, Renderer, Input } from '@angular/core';
import { AlvTableTemplateComponent } from '../alv-table-template/alv-table-template.component';
import * as $ from 'jquery';

@Component({
  selector: 'app-alv-table-template-detail',
  templateUrl: './alv-table-template-detail.component.html',
  styleUrls: ['./alv-table-template-detail.component.css']
})
export class AlvTableTemplateDetailComponent implements OnInit {

  @Input() index:number = -1;

  guid:any;
  cell:any=null;
  

  constructor(@Inject(AlvTableTemplateComponent)  private parent: AlvTableTemplateComponent,
          private elt:ElementRef, private renderer:Renderer ) {
  }

  ngOnInit() {
    //console.log(this.cell.length);
  }

  ngAfterViewInit() {
    //var textNode = this.elt.nativeElement.childNodes[0];
    //var textInput = textNode.nodeValue;
    //this.renderer.setText(textNode, textInput.toUpperCase());
    this.cell = $(this.elt.nativeElement).find('td');
  }

}



