import { Component, OnInit,Input, Inject } from '@angular/core';
import { AlvTableTemplateComponent } from '../alv-table-template/alv-table-template.component';

@Component({
  selector: 'app-alv-table-template-header',
  templateUrl: './alv-table-template-header.component.html',
  styleUrls: ['./alv-table-template-header.component.css']
})
export class AlvTableTemplateHeaderComponent implements OnInit {

  @Input() startCol:number=-1;
  @Input() endCol:number=-1;

  @Input() textAlign:string="";

  @Input() width:number=80;
  @Input() colspan:number=1;
  @Input() index:number=-1;
  //@Input() order:number=-1;
  @Input() dataField:string="";
  @Input() sortable:boolean=false;

  sortedBy:string="";   // "" - not sorted, "asc" - ascending, "desc" - descending
  order:number;

  constructor(@Inject(AlvTableTemplateComponent)  private parent: AlvTableTemplateComponent ) {

    parent.tblColumns.push(this);

   }

  ngOnInit() {
    this.order = this.index;
  }

  ngClass():any{
    let isCenter:boolean=false;
    let isLeft:boolean=false;
    let isRight:boolean=false;

    let just:string=this.textAlign;

    if(just){

      isCenter = (just=='center');
      isLeft = (just=='left');
      isRight = (just=='right');

    }else{

      let colDef:any = this.parent.options.colDefs[this.index];

      if(colDef){

        just=colDef.justify;

        if(just){
          isCenter = (just=='center');
          isLeft = (just=='left');
          isRight = (just=='right');
        }
      }
  
    }


    /*return {
      'text-center':isCenter,
      'text-left':isLeft,
      'text-right':isRight,
    }*/
    if(this.textAlign)console.log('this.textAlign:'+this.textAlign);
    
    return {
      'text-left': this.textAlign=='left',
      'text-center': this.textAlign=='center',
      'text-right': this.textAlign=='right',
    }
  }

  colWidth(idx):number{

    if(this.startCol!=-1 && this.endCol!=-1){
      this.colspan=this.endCol-this.startCol + 1;
      let idx:number;
      let ret:number = 0;
      for(idx=this.startCol;idx<=this.endCol;idx++){
        ret+=this.parent.options.colDefs[idx].width;
      }
      return ret;

    }else{
      let colDef:any = this.parent.options.colDefs[idx];
      if(colDef){
        return this.parent.options.colDefs[idx].width;
      }else{
        return -1;
      }
  
    }

    
  }

  headerClicked(e){

    if(!this.sortable) return;

    let data:Array<any>=this.parent.data;

    let desc:boolean = (this.sortedBy=="asc");
    let key = this.dataField;

    if(desc){

      this.sortedBy="desc"

      data.sort((a,b) => {
        return (a[key] < b[key]) ? 1 : ((b[key] < a[key]) ? -1 : 0);
      })
    
    }else{

      this.sortedBy="asc"

      data.sort((a,b) => {
        return (a[key] > b[key]) ? 1 : ((b[key] > a[key]) ? -1 : 0);
      })
    }

  }

}



