 weekCount(year:number, month_number:number) {

    // month_number is in the range 1..12

    var firstOfMonth = new Date(year, month_number-1, 1);
    var lastOfMonth = new Date(year, month_number, 0);

    var used = firstOfMonth.getDay() + lastOfMonth.getDate();

    return Math.ceil( used / 7);
}  

  weekCountX(yr:number, mo:number):number{
    let ret:number=1;
    let curDay:number = 1;
    let dt:Date = new Date(yr,mo,curDay);
    let dte:Date = new Date(yr,mo+1,0);

    let dow = dt.getDay();
    let i:number;

    for(i=2;i<7;i++){
      curDay += (7-dow + 1);
      dow = 0; // set to sunday;
      if(curDay>dte.getDate()){
        break;
      }else{
        ret++;
      }
    }

    return ret;
  }

