  getColWidth(id:string,pos:number):number{
    let tbl:any=$('#'+id);
    if(tbl.length){

      let cols:any=$(tbl.children('tbody').children('tr')[0]).children('td');

      return $(cols[pos]).width();

    }else{
      return 50;
    }
  }

  getAbsWidthVals(arrInput:Array<any>,reference:number,scrollWidth?:number):Array<number>{
    
    //const scrollWidth:number=40;
    let i:number;
    let curVal:number;
    let iCurrentValue:number=0;
    let iTotalAbsolute:number=0;
    let iRemainingWidth:number=0;
    let iValStr:string;
    let withPercent:boolean=false;

    let arr:Array<number>=[];

    if(scrollWidth==undefined)scrollWidth=0;

    // get total absolute value
    for(i=0;i<arrInput.length;i++){
      iValStr=String(arrInput[i]);
      if(iValStr.indexOf('%')==-1){
        // absolute value
        arr[i]=parseFloat(iValStr);
        iTotalAbsolute+=arr[i];
        
      }else{
        withPercent=true;
      }
    }

    if(withPercent){
      iRemainingWidth = reference - iTotalAbsolute - scrollWidth;
      for(i=0;i<arrInput.length;i++){
        iValStr=String(arrInput[i]);
        if(iValStr.indexOf('%')!=-1){
          arr[i] = Math.abs(iRemainingWidth * parseFloat(iValStr)/100);
          }
        }
    }

/*    for(i=1;i<arr.length;i++){
      arr[i]=parseFloat(arr[i])+parseFloat(arr[i-1])    
    }*/

    return arr;

  }  

  getReactiveTableHeight(tblID:string,cardID:string,offBottom?:number,minHeight?:number):number{
    if(minHeight==undefined)minHeight=200;

    let card=$('#'+cardID);
    let tbl=$('#'+tblID);

    if(card.length==0 || tbl.length==0)return minHeight;

    let cardPos=card.position()
    let cardOff=card.offset()

    let tblPos=card.position()
    let tblOff=card.offset()

    if(offBottom==undefined)offBottom=cardOff.top;

    let tblHead=tbl.children('thead')
    let tblBody=tbl.children('tbody')

    let tblTrueTop=tblBody.offset().top - cardOff.top;


    //let newHeight = $(window).height() - tblTrueTop - offBottom-this.bannerHeight
    let offFoot:number=0;
    let ftr =  $('.card-footer');
    if(ftr.length)offFoot=ftr.height();
    

    let newHeight = $(window).height() - tblTrueTop - offBottom-this.bannerHeight  -  offFoot;

    return newHeight > minHeight ? newHeight : minHeight;
  }

