  acnBtnClass(action:String,disable?:boolean):object{

    if(disable==undefined)disable=false;

    let retVal:object={}
    switch(action){
      case 'delete':
      case 'edit':
      case 'add':
        if(!disable)disable = this.modePersonnel!='normal';
        retVal = {
          "disabled":disable
        }
        break;
      case 'save':
      case 'cancel':
        retVal = {
          "disabled":(this.modePersonnel=='normal'),
          "btn-primary":(this.modePersonnel!='normal')
        }
        break;
  }
    return retVal
  }

