  subsSaveTeam:ISubscription
  saveTeam():ISubscription{
    let newTeam:boolean=this.currTeam.id==-1;

    // validate data
    let tm:dataTeam=this.currTeam;
    let err:string='';
    if(tm.name==''){
      this.setTempMessage(undefined,"Please enter a team name");
      //setTimeout(()=>$('#teamName').focus(),10);
      G.setFocus('teamName');
      return undefined;
    }

    tm.group = tm.core ? "Core" : "Non-Core";

    this.subsSaveTeam = this.processData({
      method:"saveTeam",
      data:{
        id:tm.id,
        name:tm.name,
        description:tm.description,
        order:tm.order,
        core:tm.core,
        upman:tm.upman
      },
      subscription:this.subsSaveTeam,
      isPosting:true,
      processMessage:"Saving team details. Please wait...",
      onSuccess:data=>{
        //set
        //this.currTeam.order=data['resInt32']
        this.udf.isTeamNormal(true);
        this.setTempMessage("msgTempInfoPrompt",(newTeam ? "New team" : "Team " + tm.name ) + " was successfully "+ (newTeam ? " created." : " saved."))

        if(newTeam){
          tm.id=data['resInt32'];      // save new id
          tm.order=data['resInt32B'];  // save new order

          this.currTeamObj=new dataTeam()
          this.currTeamObj=this.cloneObject(tm);
          
        }

        if(newTeam){

          // append new team to datTeams array
          this.datTeams.push(this.currTeamObj);

          this.setOtherItemGroupings();
          
        }else{
          let cTm:dataTeam=this.currTeamObj;
          cTm.name=tm.name;
          cTm.description=tm.description;
          cTm.core=tm.core;
          cTm.upman=tm.upman;
          cTm.group=tm.group;

        }

      }
    });

    return this.subsSaveTeam;

  }

  subsDeleteTeam:ISubscription;
  deleteTeam(data:any,go?:boolean):ISubscription{
    
    let postData:any={}
    
    if(this.udf.isTeamDeleteAlert()){
      let tm:dataTeam=this.currTeamObj;
      postData.id = tm.id;
      postData.name = tm.name;

      this.subsDeleteTeam = this.processData({
        method:"deleteTeam",
        data:postData,
        isPosting:true,
        processMessage:"Deleting team '"+ this.currTeamObj.name +"'. Please wait...",
        subscription:this.subsDeleteTeam,
        onSuccess:data=>{

          // set alerts
          this.udf.isTeamNormal(true);
          this.setTempMessage(undefined,"Team '"+ this.currTeamObj.name +"' has been deleted.")

          // remove client-side data
          let idx:any=this.datTeams.findIndex(item=>{
            return item.id==tm.id;
          })        

          this.datTeams.splice(idx,1);
          this.setOtherItemGroupings();
          this.currTeam=new dataTeam();
          this.currTeamObj=new dataTeam();;
          
        }
      })


      // delete entire team
    }else if(this.udf.isTeamMemberDeleteAlert()){
      // delete team member

      let tmm:teamMember=this.currDelObj;

      postData.id = tmm.id;
      postData.name = this.idxPersonnel['r'+tmm.perID]['fullName'];


      this.subsDeleteTeam = this.processData({
        method:"deleteTeamMember",
        data:postData,
        isPosting:true,
        subscription:this.subsDeleteTeam,
        processMessage:"Deleting team member '"+ postData.name +"'. Please wait...",
        onSuccess:data=>{
          this.udf.isTeamNormal(true);
          this.setTempMessage(undefined,"Team member '"+ postData.name +"' was removed.")
          // remove item from the cached member then clone to the current team object
          let idx:any=this.currTeam.members.findIndex(item=>{
            return item.perID==postData.id;
          })        

          this.currTeam.members.splice(idx,1);
          this.currTeamObj.members=this.cloneObject(this.currTeam.members);


        }
      })

    }

    return this.subsDeleteTeam;
  }

