  getActivities(force?:boolean,onDataLoad?:Function,beforeLoad?:Function,onError?:Function):ISubscription{

    //this.clearSubscription(this.subsActivities);

    let activity:dataActivity;
    if(force==undefined)force=false;

    if(force || this.datActivities.length==0){

      if(beforeLoad!=undefined && beforeLoad!=null)beforeLoad();

      this.setLoading("Loading activities. Please wait...");
      
      return this.http
        .get(G.getUrlRequest("oplanData","getActivities"))
        .subscribe(
          
          data => {

            let extactDate:Date = G.epochToDate(data['extracted']);

            this.datActivities=[];
            this.idxActivity={};

            data['rows'].forEach(element => {
              /*
              export class dataActivity{
                id:number;
                name:string;
                description:string;
                startDate:string;
                endDate:string;
                personnels:Array<number>;
                members:Array<object>;

                id:number;
                teamID:number;
                perID:number;
                startDate:string;
                endDate:string;
                mob:boolean;
                demob:boolean;
                night:boolean;

            }*/
              activity=new dataActivity();
              activity.id=Number(element[0]);
              activity.name=element[1];
              activity.description=element[2];
              activity.group = (element[3].length ? element[3].substr(0,4) : "");
              activity.order=element[3];
              activity.startDate=G.shortToLongDate(element[3]);
              activity.endDate=G.shortToLongDate(element[4]);
              activity.type = element[5];
              activity.showInChart = (element[6]==1);
              activity.noPOBCount = (element[7]==1);
              activity.ready = element[8];

              activity.site = element[10];
              activity.vessel = (element[11]==1);
              activity.upmanning = (element[12]==1);

              activity.extracted = extactDate;

              activity.members=[];
              activity.personnels=[];

              // if processed activity is the same as the current activity, set extract date of the
              // current activity the same as the extract date of the activity being processed
              if(this.currActivity){
                if(this.currActivity.id==activity.id){
                  // probably a good idea to also update details of the currentActivity
                  // with the details of the processed activity ????
                  this.currActivity.extracted=extactDate;
                  if(this.currActivityObj.id==activity.id)this.currActivityObj=activity;
                }
              }


              //recid, tid, pid, sd, ed, mob, demob, night
              //"2","1","179","20170328","20170411","1","1","0"
              
              element[9].forEach(mem =>{
                let amem:activityMember = {
                  sort:"",
                  id:Number(mem[0]),
                  idAct:Number(element[0]),
                  teamID:Number(mem[1]),
                  perID:Number(mem[2]),
                  startDate:G.shortToLongDate(mem[3]),
                  endDate:G.shortToLongDate(mem[4]),
                  mob:mem[5]==1,
                  demob:mem[6]==1,
                  night:mem[7]==1,
                  isDay:mem[8]==1,
                  noPOB:(element[7]==1),
                  coyID:mem[9],
                  posID:mem[10],
                  grpID:mem[11]
                }
                amem.sort=this.activityMemberSortString(amem)
                activity.members.push(amem);
              });

              this.datActivities.push(activity);
              this.sortActivityMembers(activity.members);

              this.idxActivity['r' + activity.id]=activity;

              // process activity types
              if(data['types']!=undefined){
                this.datPriorities=[];
                data['types'].forEach(type=>{
                  let tp:lookupTemplate=new lookupTemplate();
                  tp.id = Number(type[0]);
                  tp.name = type[1];
                  tp.order = Number(type[2]);
                  //
                  this.datPriorities.push(tp);
                })
              }

            });

            if(onDataLoad!=undefined && onDataLoad!=null){
              onDataLoad(this.datPosition);
            }

            this.noLoading();

          }, 
          err => {
            if(onError!=undefined && onError!=null){
              onError(err);
            }
            this.withError(err);
          }
        );
  
    }else{
      if(onDataLoad!=undefined)onDataLoad(this.datActivities);   
    }
    return undefined
  }


