  isExisting(objArr:Array<object>,fieldName:string,fieldValue:any):boolean{
    let ret:boolean=false;
    let idx:number = objArr.findIndex(x => x[fieldName]==fieldValue);
    return ret;
  }

  getItemIndex(objArr:Array<object>,fieldName:string,fieldValue:any):number{
    return objArr.findIndex(x => x[fieldName]==fieldValue);
  }  


  getActivityGroupings(findAct?:dataActivity):Array<object>{

    let ret:Array<object>=[];

    let inputArray:Array<object>=this.datActivities;

    if(this.udf.searchText.length){
      inputArray = inputArray.filter(item => 
        { return item['name'].toLowerCase()!="-" && item['name'].toLowerCase().
          indexOf(this.udf.searchText.toLocaleLowerCase())!=-1 });
    }

    //this.datActivities.forEach((act:dataActivity)=>act.parent=null);

    inputArray.forEach((act:dataActivity)=>{

      act.parent=null;

      let sd:Date = G.longToDate(act.startDate);
      let ed:Date = G.longToDate(act.endDate);

      let sy:number=sd.getFullYear();
      let sm:number=sd.getMonth();

      let ey:number=ed.getFullYear();
      let em:number=ed.getMonth();
      
      let cy:number=sy;
      let cm:number=sm;

      let moSpan:number=0;
      let tmpDt:Date;

      while(true){
        moSpan++;

        let idxYear:number=this.getItemIndex(ret,'year',cy);
        let objYear:object;
        if(idxYear==-1){
          // year object is not yet existing
          objYear={"year":cy ,exp:false,"months":[]}
          ret.push(objYear);
        }else{
          objYear = ret[idxYear];
        }

        let idxMonth:number=this.getItemIndex(objYear['months'],'month',cm);
        let objMonth:object;
        if(idxMonth==-1){
          // month is not yet existing
          objMonth={"month":cm,exp:false,"priorities":[],parent:objYear};
          objYear['months'].push(objMonth);
        }else{
          objMonth=objYear['months'][idxMonth];
        }
        
        let idxPriority:number=this.getItemIndex(objMonth['priorities'],'priority',act.type);
        let objPriority:object;
        if(idxPriority==-1){
          // month is not yet existing
          let typeObj:object=undefined;
          let order:number=999;
          let priorityName:string="<UnAssigned>";

          if(act.type!=0)typeObj = this.datPriorities[this.getItemIndex(this.datPriorities,"id",act.type)];
          if(typeObj!=undefined){
            order=typeObj['order'];
            priorityName=typeObj['name'];
          }

          objPriority={"priority":act.type,
            "order":order,
            "name":priorityName,
            "activities":[],
            exp:false,
            parent:objMonth};
          objMonth['priorities'].push(objPriority);

        }else{
          objPriority=objMonth['priorities'][idxPriority];
        }

        //if(act.parent==null)
        act.parent=objPriority;

        objPriority['activities'].push(act);
                
        

        if(cy==ey && cm==em)break;

        cm++;

        tmpDt=new Date(cy,cm,1);

        cy=tmpDt.getFullYear();
        cm=tmpDt.getMonth();

      }

    });

    if(ret.length){
      // sort data...
    
      G.sortBy(ret,"year",true);
      ret.forEach(yr=>{
        G.sortBy(yr['months'],"month",true);
        yr['months'].forEach(mo=>{
          G.sortBy(mo['priorities'],"order");
          mo['priorities'].forEach(pri=>{
            G.sortBy(mo['activities'],"name");
          });
        });
      });

    }

/*
    if(ret.length){
      let yr=ret[1];
      yr['exp']=true;
      if(yr['months'].length){
        let mo=yr['months'][4];
        mo['exp']=true;
        if(mo['priorities'].length){
          mo['priorities'][1]['exp']=true;
        }
      }
    }

*/

    if(this.udf.searchText.length || this.onActivityLoadAction){
      //let ret:Array<object>=ret;

      
      if(this.udf.searchText.length){
        // search text is not empty, expand all parent nodes
        for(let yIdx:number=0;yIdx<ret.length;yIdx++){
          ret[yIdx]['exp']=true;
          let mos:Array<any> = ret[yIdx]['months'];
          for(let mIdx:number=0;mIdx<mos.length;mIdx++){
            mos[mIdx]['exp']=true;
            let pris:Array<any> = mos[mIdx]['priorities'];
            for(let pIdx:number=0;pIdx<pris.length;pIdx++){
              pris[pIdx]['exp']=true;
            }
          }
        }
      }
      

      if(ret.length){
        let yr=ret[0];
        yr['exp']=true;


        if(yr['months'].length){
          let mo=yr['months'][0];
          mo['exp']=true;
          if(mo['priorities'].length){
            mo['priorities'][0]['exp']=true;

            // set default activity (initial...)

            // initial activity under priority 0 ...
            let defAct:dataActivity=mo['priorities'][0]['activities'][0];

            // find default activity by id
            let defActIDSaved = localStorage.getItem("defActID");

            if(defActIDSaved){
              let defActID:number = Number(defActIDSaved);
              let defActTemp = this.datActivities.find((act)=>{return act.id == defActID});
              if(defActTemp) defAct = defActTemp; // if saved activiity id search is successful
            }
            
            this.scatterActivity(defAct) ;
          }
        }
        this.onActivityLoadAction=false;
      }

      

    }

    this.activityGroupings=ret;

    this.searchActivity(findAct);

    return this.activityGroupings;
  }

  searchActivity(act?:dataActivity){
    setTimeout(()=>{
      if(act==undefined)act=this.currActivity;
      if(act.id==-1) return;

      let grp:Array<object>=this.activityGroupings
      // get year
      let yr:any=grp.find((e:any)=>{
        return (e.year==G.longToDate(act.endDate).getFullYear());
      })

      if(yr!=undefined){
        yr.exp=true;

        let mo:any=yr['months'].find((e:any)=>{
          return (e.month==G.longToDate(act.endDate).getMonth());       
        });

        if(mo!=undefined){
          mo.exp=true;

          let pri:any=mo['priorities'].find((e:any)=>{
            return (e.priority==act.type);
          });

          if(pri!=undefined)pri.exp=true;
          
        }
        
      }
      
    },100);
  }

  setOtherItemGroupings(){

    let module:string=this.udf.currentModule;
    let inputArray:Array<object> = (module =='Teams' ? this.datTeams : this.datActivities);

    this.itemsGroupings=G.groupBy(inputArray.filter(item => 
        { return item['name'].toLowerCase()
        .indexOf(this.udf.searchText.toLocaleLowerCase())!=-1 }),
        'group',
        undefined,undefined,undefined,'order');            

    if(module=='Activities'){
      this.getActivityGroupings();
    }
            
    this.setItemGroupInitialExpand();

    if(this.udf.currentModule=='Teams'){

      // scatter first team of the first group on first time loading
      if(this.itemsGroupings.length>0){
        if(this.itemsGroupings[0]['data'].length>0 && this.currTeamObj.id==-1){
          this.scatterTeam(this.itemsGroupings[0]['data'][0]);
        }
      }
      
      // set user lookup group
      this.setTeamUserSelection();

    }else if(this.udf.currentModule=='Activities'){

      // scatter first activity of the first group on first time loading

      if(this.itemsGroupings.length>0){
        if(this.itemsGroupings[0]['data'].length>0 && this.currActivityObj.id==-1){

          // set default activity ...
          this.scatterActivity(this.itemsGroupings[0]['data'][0]);
        }
      }

      this.userSelection=G.groupBy(this.datPersonnel.filter(item=>{
        return item['id']!=0
      })
        ,"nameGroup");


    }else{

      if(this.itemsGroupings.length>0){
        if(this.itemsGroupings[0]['data'].length>0 && this.currPersonnelObj.id==-1){
          this.scatterPersonnel(this.itemsGroupings[0]['data'][0]);
        }
      }      
    }

  }

