  activityMemberSortString(mem:any):string{

    let team:dataTeam=this.idxTeam['r'+mem.teamID];
    let per:dataPersonnel=this.idxPersonnel['r'+mem.perID];
    

    let grpName:string="";
    let grp:any=this.config.teamGroupsArr.find((e)=>{return e.id==mem.grpID})

    if(grp)grpName=grp.name;

    /*
    let act:dataActivity = this.idxActivity['r'+mem.idAct];

    let sortStr:string=""

    if(team.core){
      sortStr="0" + G.pad0(team.order,2);
    }else{
      sortStr="1" + G.pad0(team.order,2);
    }*/

    return (team.core ? "0" : "1") + G.pad0(team.order,2) + " " + 
      (grpName+" ".repeat(50)).substr(0,50) + 
      (per.fullName + " ".repeat(50)).substr(0,50)+
      G.longToShortDate(mem.startDate);
      
  }

