  isExpandAll():boolean{
    let fa:any=$('#allExpCol');
    if(fa.length==0)return false;
    return fa.hasClass('fa-angle-double-down');
  }

  expAllTeamView(){
    let fa:any=$('#allExpCol');
    if(fa.length==0)return;
    //alert(fa.children('.fa-angle-double-down').length);

    let mems:any = this.oplandb.currActivity.members;
    let teams:any = G.groupBy(mems,"teamID");
    let isExpandAll:boolean=fa.hasClass('fa-angle-double-down');

    teams.forEach(element=>{
      let team:any=this.oplandb.idxTeam['r'+element.type];

      if(!team.expView)team.expView=true;

      if(isExpandAll && !team.expView){
        team.expView=true;
      }else if(!isExpandAll && team.expView){
        team.expView=false;
      }
    });

  }

  clsExpColAll(){
    //oplandb.currActivity.members
    let mems:any = this.oplandb.currActivity.members;
    let teams:any = G.groupBy(mems,"teamID");
    let expCnt:number=0;
    teams.forEach(element => {
      let team:any=this.oplandb.idxTeam['r'+element.type];
      if(!team.expView)expCnt++;
    });
    let half:number = teams.length/2;
    let isHalf:boolean=(expCnt<=parseInt(String(half)));
    return {
      'fa-angle-double-down':!isHalf,
      'fa-angle-double-up':isHalf
    }

  }

  withConflict(mem:any):boolean{
    //oplan.currActivity.conflicts['r'+mem.id]!=undefined
    //return true;

    if(this.oplandb.currActivity.conflicts==null){
      return false;
    }else{
      return this.oplandb.currActivity.conflicts['r'+mem.id]!=null;
    }

  }
  withConflictInTeam(currTeamID:any,retType?:string,noTeam?:boolean):object{
    let ret:object=null;
    let act:dataActivity=this.oplandb.currActivity;
    let m:activityMember;
    let confArr:Array<any>=[];
    let warnArr:Array<any>=[];

    if(retType==undefined)retType=""
    if(noTeam==undefined)noTeam=false;

    if(retType=="" || retType=="C"){
      confArr = act.members.filter((mem:activityMember)=>{
        return (mem.teamID==currTeamID || noTeam) && mem.confCount>0
      });
    }

    if(retType=="" || retType=="W"){
      warnArr=act.members.filter((mem:activityMember)=>{
        return (mem.teamID==currTeamID || noTeam) && mem.warnCount>0
      });

    }

    return  {
              W:warnArr, withWarning  :(warnArr.length>0),
              C:confArr, withConflict :(confArr.length>0),
              E:(warnArr.length>0 || confArr.length>0)
            };
  }

