import { Component, OnInit, TemplateRef, Input } from '@angular/core';

@Component({
  selector: 'app-alv-table-panel',
  templateUrl: './alv-table-panel.component.html',
  styleUrls: ['./alv-table-panel.component.css']
})
export class AlvTablePanelComponent implements OnInit {

  constructor() { }

  @Input()
  showUL:boolean=false;

  @Input()
  showLL:boolean=false;

  @Input()
  showUR:boolean=false;

  @Input()
  showLR:boolean=false;

  @Input()
  headerTemplate:TemplateRef<any>;

  ngOnInit() {
  }

}



