import { Component, OnInit, Input, ComponentFactoryResolver } from '@angular/core';
import { dataPersonnel } from '../../models/data-models';
import * as $ from 'jquery';
import * as Global from  '../../app.globals';
import * as U from  '../../app.udfs';

import { AppOplpanDataService } from '../../app.oplandata';

@Component({
  selector: 'app-frm-personnel',
  templateUrl: './frm-personnel.component.html',
  styleUrls: ['./frm-personnel.component.css']
})
export class FrmPersonnelComponent implements OnInit {

  actsCols:Array<any>=["100%",100,100,100,100,100];
  actsColsPX:Array<number>;
  actsBodyHeight:number=150;

  lkpCompanies:Array<object>=[
    {id:1, name: "Soga"},
    {id:2, name: "DOF"},
    {id:3, name: "SPEX"}
  ]

  lkpCompanies2:Array<object>;

  lkpPositions:Array<object>=[
    {id:1, name: "Electrical"},
    {id:2, name: "Assessor"},
    {id:3, name: "Integrity Engineer"},
    {id:4, name: "Programmer"}
  ]

  actionMode:string="add";
  isDirty:boolean=false;

  loadLookups:boolean=false;

  errorMessage:string;

  @Input() currPersonnel:dataPersonnel;

  constructor(public appDataService:Global.AppDataService,
    public oplandb:AppOplpanDataService,
    public udf: U.AppUDFService) { }

  ngOnInit() {

    this.oplandb.itemsGroupings=[];

    this.loadLookups=false;

    this.oplandb.clearAllSubscription();

    this.oplandb.getPersonnel(false,data=>{
      this.oplandb.getPersonnelActiveAcivities();
      this.oplandb.setPersonnelNavGroup();
    })

    this.setTableColWidths();
    
    this.oplandb.getCredentials();

    this.udf.isPersonnelNormal(true);

  }

  onResize(e){
    this.setTableColWidths();
  }

  setTableColWidths(){

    this.actsColsPX=this.udf.getAbsWidthVals(this.actsCols,$('#acts').width());
    this.actsBodyHeight=this.udf.getReactiveTableHeight('acts','perCard',40,35);

  }
      
  savePersonnel(){
    if(this.udf.modePersonnel=="normal")return;
    this.oplandb.savePersonnel();
  }  

  results:any;
  cancelUpdate(){
    this.udf.modePersonnel="normal";
    if(this.oplandb.currPersonnelObj.id!=-1){
      this.oplandb.resetMessages();
      this.oplandb.scatterPersonnel();
    }

  }

  clearEntry(row:any,fld:string){
    row[fld]="";
  }

  dispAlert(mode:string):boolean{
    return this.udf.isPersonnelDeleteAlert();
  }

  calEdit(row:any):boolean{
    if(this.udf.modePersonnel=='normal') return false;
    if(!row.current) return false;
    if(this.oplandb.currPersonnel.id<0) return false;
    //if(!row.required) return false;
    return true;
  }

  curComp(row){
    let cRow:any = this.oplandb.datCertificates.find(e=>{
      return e.current;
    });
    if(cRow){
      if(cRow.id!=row.id){
        cRow.current=false;
      }
    }
    row.current=true;
  }

  btnClick(mode:string){
    switch(mode){
      case 'DEL':
        if(this.oplandb.currPersonnelObj.id==-1){
          this.oplandb.setTempMessage(undefined,"Please a personnel to delete.");
        }else{
          this.udf.isPersonnelDeleteAlert(true);
        }
        
        break;
      case 'goDelete':
        this.oplandb.deletePersonnel();
        break;
      case 'noDelete':
        this.udf.isPersonnelNormal(true);
        break;
    }
  }

  onButtonFocus(e){
    //b.blur();
    if(this.udf.modePersonnel=='normal')e.target.blur();
    
  }

  setGender(gender){  
    if(this.udf.modePersonnel=='normal')return;
    this.oplandb.currPersonnel.gender=gender;
  }

  toggleXL(){
    if(this.udf.modePersonnel=='normal')return;
    this.oplandb.currPersonnel.xl=!this.oplandb.currPersonnel.xl;
  }

  changeRecord(evt){
    this.loadLookups=true;
    this.oplandb.dataActionClicked(evt)
  }

  toggleRequired(row:any){
    if(this.udf.modePersonnel=='normal' || this.oplandb.currPersonnel.id==-1 || row.mandatory==true)return;
    row.required = !row.required;
  }

  changeDate(row:any,fld:string){

  }

  certSource():Array<any>{
    return  this.oplandb.datCertificates.slice(1,10);
  }

  openCalendar(id:string){
    Global.setFocus(id);
  }

  displayExpiry(cert:any):string{
    let ret:string='';
    if(cert.expiry){
      ret = cert.expiry;
    }else if(cert.issued){
      //let c:any=this.oplandb.datCertificates.find(e=>{ return e.id==cert.id});
      //if(c){
        // check if frequency is set
      //  console.log("Certificate:")
      //  console.log(c)
      //}
      if(cert.frequency!=0){
        ret ="( " + Global.dateToString(Global.addDays(Global.stringToDate(Global.longToShortDate(cert.issued)),365.25 * cert.frequency),true) + " )"
      }
    }
    return ret;
  } 


}



