import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';

@Component({
  selector: 'app-frm-table-panels',
  templateUrl: './frm-table-panels.component.html',
  styleUrls: ['./frm-table-panels.component.css']
})
export class FrmTablePanelsComponent implements OnInit {

  constructor() { }

  vertSeparatorPos:number=400;
  headSeparatorPos:number=100;

  windowWidth:number;
  windowHeight:number;

  divMain:any =$('#d');

  contWidth(){
    //return $(document).width();
    return window.innerWidth;
  }

  contHeight(){
    return $(document).height();
  }

  ngOnInit() {
    this.divMain =$('#d');
    let cont = $('#d');

    this.setWindowSizeVars();
  }

  iClick(e){
    alert('hhh');
  }

  onResize(e){
    this.setWindowSizeVars()
  }

  setWindowSizeVars(){
    this.windowWidth=$(window).width();
    this.windowHeight=$(window).height();
  }

  onElementScroll(e){
    if(e.srcElement.id=='rightDetails'){
      $('#rightHeader').scrollLeft($(e.srcElement).scrollLeft())
    }else if(e.srcElement.id=='leftDetails'){
      $('#leftHeader').scrollLeft($(e.srcElement).scrollLeft())
    }
  }

}



