export class dataActivity{
    constructor(){
        this.id=-1;
        this.site="SWP";
        this.name="";
        this.description="";
        this.startDate="";
        this.endDate="";
        this.type=0;
        this.showInChart=true;
        this.noPOBCount=false;
        this.ready="Amber";
        
        this.members=[];
        this.conflicts=null;

        this.parent=null;
        this.vessel=false;
        this.upmanning=false;

        this.extracted=new Date();
        this.removed=false;
        this.obsolete=false;
        
    }

    id:number;
    site:string;
    name:string;
    group:string;
    order:string;
    description:string;
    startDate:string;
    endDate:string;
    type:number;
    showInChart:boolean;
    noPOBCount:boolean;
    ready:string;
    personnels:Array<number>;       // obsolete... not used
    members:Array<object>;

    competencies?:Array<any>;
    conflicts?:object;

    parent?:any;
    vessel:boolean;
    upmanning:boolean;

    extracted:Date;
    obsolete:boolean;
    removed:boolean;

    
}

export class activityMember{

    constructor(){
        this.id=-1;
        this.idAct=-1;
        this.perID=0;
        this.startDate="";
        this.endDate="";
        this.mob=true;
        this.demob=true;
        this.night=false;
        this.isDay=false;
        this.noPOB=false;
        this.coyID="0";
        this.posID="0";
        this.grpID="0";
        this.sort="";
        this.conflicts=null;    
        this.warnMessage="";
        this.infoMessage="";
        this.confMessage="";
        this.warnCount=0;
        this.infoCount=0;
        this.confCount=0;
        }

    id:number;
    idAct:number;
    teamID:number;
    perID:number;
    startDate:string;
    endDate:string;
    mob:boolean;
    demob:boolean;
    night:boolean;
    isDay:boolean;
    noPOB:boolean;
    coyID:any;
    posID:any;
    grpID:any;
    sort:string;
    conflicts?:Array<activityConflict>;
    warnMessage?:string;
    infoMessage?:string;
    confMessage?:string;

    compWarnExpired?:number;    // red
    compWarnNotTaken?:number;   // red
    compWarnExp7Days?:number;   // orange
    compWarnExp30Days?:number;  // yellow
    compWarnLastDay?:number;  // yellow
    compWarnCompliant?:number;  // green

    compWarnExpiredTitle?:string;  
    compWarnNotTakenTitle?:string; 
    compWarnExp7DaysTitle?:string; 
    compWarnExp30DaysTitle?:string;
    compWarnLastDayTitle?:string;  
    compWarnCompliantTitle?:string;

    compTitle?:string;

    warnCount?:number;
    infoCount?:number;
    confCount?:number;
}

export class activityConflict{
    memberId:number;
    personnelId:number;

    activityId1:number;
    teamId1:number;
    startDate1:Date;
    endDate1:Date;

    activityId2:number;
    teamId2:number;
    startDate2:Date;
    endDate2:Date;
    
    isContinuation:boolean;
    isContinuationMob:boolean;
    isContinuationDemob:boolean;

}

export class dataPersonnel{

    constructor(){
        this.status=1;
        this.id=-1;
        this.companyID="0";
        this.positionID="0";
        this.gender="";
        this.birthDate="";
        this.maxDays=0;
        this.xl = false;
        this.certificates=[];
    }

    id:number;
    fullName:string;   
    firstName:string;   
    lastName:string;
    nameGroup:string;
    companyID:string;
    positionID:string;
    xl:boolean;
    gender:string;
    email:string;
    maxDays:number;
    
    activityCount:number;
    activities:Array<object>;
    birthDate:string;
    birthDateShort:string;
    certificates:Array<object>;
    status:number;
}

export class dataTeam{

    constructor(){
        this.id=-1;
        this.name=""
        this.description=""
        this.order=1
        this.core=true;
        this.upman=false;
        this.members=[];
        this.group="";
        this.expView=false;
    }

    id:number;
    name:string;
    description:string;
    core:boolean;
    group:string;
    order:number;
    upman:boolean;
    members:Array<teamMember>;
    expView?:boolean;
}

export class teamMember{
    constructor(){
        this.sort=""
    }
    id:number;
    teamID:number;
    perID:number;
    shift:string;
    subTeamID:number;
    sort:string;
}

export class lookupTemplate{
    constructor(){
        this.order=0;
    }
    id:number;
    name:string;
    order?:number;
}

