import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';

@Component({
  selector: 'app-test-panels',
  templateUrl: './test-panels.component.html',
  styleUrls: ['./test-panels.component.css']
})
export class TestPanelsComponent implements OnInit {

  selectedDate:any="20 Oct 2017";
  datePickerConfig={
    format:"D MMM YYYY",
    locale:"en"
  };

  constructor() { }

  ngOnInit() {
    //alert($('.dp-picker-input').length);
    $('.dp-picker-input').addClass('form-control form-control-sm').css('width','120px').css('text-align','center');
  }

}



