import { Component, Input, Output, OnInit, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-ctl-user-select',
  templateUrl: './ctl-user-select.component.html',
  styleUrls: ['./ctl-user-select.component.css']
})
export class CtlUserSelectComponent implements OnInit {

  counterValue = 0;
  searchText:string='';
  
  @Input()
  get counter() {
    return this.counterValue;
  }

  @Output() counterChange = new EventEmitter();
  set counter(val) {
    this.counterValue = val;
    this.counterChange.emit(this.counterValue);
  }

  @Output() itemSelected:any = new EventEmitter();
  

  @Input() width:number;
  @Input() widthUnit:string;
  @Input() height:number;
  @Input() value:number;
  @Input() disableSelect:boolean;
  @Input() lookup:object;
  @Input() displayField:string;
  @Input() valueField:string;
  @Input() source:Array<object>=[];

  @Input() groupName:string;
  @Input() dataName:string;
  @Input() valueColumn:string;
  @Input() displayColumn:string='';
  @Input() fontSize:string='0.8em';

  constructor() { }

  ngOnInit() {
    //alert(this.lookup['r'+this.value]['fullName']);
    //lert(this.source[0]['data'].length);
    if(this.widthUnit==undefined)this.widthUnit='px';
  }

  itemClickedTest(user:any){
    alert('item is clicked')
  }
  itemClickTest2(){
    alert('item is clicked2')
  }

  //(click)="itemClicked(user[valueColumn],user,group)" 
  itemClicked(userID,user,team){
    //this.counter=user['id'];
    this.counter=userID;
    this.itemSelected.emit({"user":user, "team":team});
  }

  xitemDisplayText(user:object):string{
    return user[this.valueColumn];
  }

  itemDisplayText(user:object):string{

    let retVal:string='';
    //user[valueColumn]
    if(this.displayColumn==undefined)this.displayColumn='';
    if(this.displayColumn.length==0){
      // if display column is not specified, use the lookup object as source of display,
      // using the user[valueColumn] to search for the user item
      retVal=this.lookup['r' + user[this.valueColumn]][this.displayField]
    }else{
      retVal=user[this.valueColumn];
    }
    return retVal;
  }

  itemSource(group:any){
    let retVal:Array<object>=group[this.dataName];
    if(group[this.dataName].length>0){
    }

    let newArr:any = retVal.filter(item=>{
      return (this.itemDisplayText(item).toLowerCase().indexOf(this.searchText.toLowerCase())!=-1)
    })



    return newArr;
  }
  
  getTextWidth():string{
    return  (this.width-30)+this.widthUnit;
    
  }

  getDropdownWidth():string{
    return (this.width)+this.widthUnit;
  }


  trackByUser(idx,item)
  {
    return idx;
  }

  trackByGroup(idx,item)
  {
    return idx;
  }

}



