import { Component, OnInit } from '@angular/core';
import {DpDatePickerModule, DatePickerComponent} from 'ng2-date-picker'

import * as $ from 'jquery';
import * as G from  '../../app.globals';
import * as U from  '../../app.udfs';

import { AppOplpanDataService } from '../../app.oplandata';


@Component({
  selector: 'app-frm-settings',
  templateUrl: './frm-settings.component.html',
  styleUrls: ['./frm-settings.component.css']
})
export class FrmSettingsComponent implements OnInit {

  currRowID:string="";
  blockHeight:number=500;

  constructor(public udf:U.AppUDFService,
    public oplandb:AppOplpanDataService) { }

  ngOnInit() {
    this.oplandb.clearAllSubscription();
    
    setTimeout(()=>{this.onResize(null);},50);
    this.oplandb.getConfig();
  }

  onResize(e){
    this.blockHeight=$('#myBLOCK').height();
  }

  tblFullHeight():number{
    return this.blockHeight-115 + (!this.oplandb.logInfo.settingsAdmin ? 32 : 0);
  }

  tblHalfHeight():number{
    return ((this.blockHeight-115*2)/2-2) + (!this.oplandb.logInfo.settingsAdmin ? 32 : 0);
  }

  showLongDate(dt:Date,initVal?:string):string{
    if(G==undefined)return "";

    if(initVal==undefined)initVal="01 Jan 1980";
    let ret:string=G.dateToString(dt,true);

    return ret==initVal ? "( Initial )" : ret;
  }

  mouseEnter(type:string,id:any){
    this.currRowID=type+id;
  }
  mouseLeave(){
    this.currRowID=""
  }

  showCalendar(id){
    G.setFocus(id);
  }


  removeSelectedConfig(type,idx){

    let cfgObj=this.oplandb.getConfigObject(type);
    let isNoDelete:boolean=false;

    if(cfgObj==undefined)return;

    if(type!='UPER'){
      // suppress deletion of inital values
      let lastIndex = cfgObj['limits'].length-1;
      if(lastIndex != idx){
        // not the last item of the limits array
        isNoDelete = ((type+idx) != this.currRowID);
      }
    }

    if(!isNoDelete){
      this.oplandb.removeConfig(type+idx);
    }else{
      alert('not deleting...')
    }

  }

  getDeleteStyle(type,idx):object{
    let cfgObj=this.oplandb.getConfigObject(type);
    let isNoDelete:boolean=true;

    if(type!='UPER' && this.oplandb.logInfo.settingsAdmin){
      let lastIndex = cfgObj['limits'].length-1;
      if(lastIndex != idx){
        // not the last item of the limits array
        isNoDelete = ((type+idx) != this.currRowID);
      }
    }else if(type=='UPER' && this.oplandb.logInfo.settingsAdmin){
      isNoDelete = ((type+idx) != this.currRowID);
    }

    return { 
      'color' : isNoDelete ? 'transparent' : this.udf.defColorDanger,
      'cursor' :  isNoDelete ? 'default' : 'pointer'
    };

  }
  getDeleteTitle(type,idx):string{
    return ''
  }
/*
  <i *ngIf="currRowID!=('DLIM' + idx) || oplandb.isPosting" 
  (click)="removeSelectedConfig('DLIM' + idx);"
  [style.color]="getDeleteColor('DLIM' + idx)"
  [title]="getDeleteTitle('DLIM' + idx)"
  class="fa fa-times-circle float-right" aria-hidden="true" 
  style="width:20px;font-size:1.2em;"></i>
*/


}



