import { AfterContentInit, Component, OnInit, Output, EventEmitter } from '@angular/core';
import { dataPersonnel, dataActivity, lookupTemplate, dataTeam } from '../../models/data-models';
import * as Global from  '../../app.globals';
import * as $ from 'jquery';

import  { AppUDFService  } from '../../app.udfs';

import { AppOplpanDataService } from '../../app.oplandata';

@Component({
  selector: 'app-nav-list',
  templateUrl: './nav-list.component.html',
  styleUrls: ['./nav-list.component.css']
})
export class NavListComponent implements OnInit {

  @Output() itemClicked:any=new EventEmitter();
  @Output() sortSelectionClicked:any=new EventEmitter();
  @Output() thisComponent:any=new EventEmitter();

  expAll:boolean=false;
/*
  currentItem:dataPersonnel=null;
  currPersonnel:dataPersonnel=null;
  currActivity:dataActivity=new dataActivity();
  currTeam:dataTeam=null;
*/
  itemsFiltered:Array<dataPersonnel>;

  itemsRaw:Array<object>;
  errorMessage:string;

  itemsByLastName:Array<object>;
  itemsByDiscipline:Array<object>;
  itemsByCompany:Array<object>;

  itemsGroupings:Array<object>;

  navSortBy:string="Activities";

  monthNames:Array<string> = [
    "January", "February", "March",
    "April","May","June",
    "July","August","September",
    "October","November","December"
  ]  

  lkpCompanies:Array<lookupTemplate>=[
    {id:1, name: "Soga"},
    {id:2, name: "DOF"},
    {id:3, name: "SPEX"}
  ]

  lkpPositions:Array<lookupTemplate>=[
    {id:1, name: "Electrical"},
    {id:2, name: "Assessor"},
    {id:3, name: "Integrity Engineer"},
    {id:4, name: "Programmer"}
  ]

//  constructor(public appDataService:Global.AppDataService) { }
constructor(public appDataService:Global.AppDataService,
            public oplandb:AppOplpanDataService,
            public udf:AppUDFService) { }


  itemClass(item,idx):object{
    return {
      "list-group-item":true,
      "cusi": (idx !=3 ? true : false),
      "cusg2": (idx ==3 ? true : false)
    }
  }

  itemGroupName(value,group){

    let obj:object=null;

    if(group=='position'){
      obj = this.oplandb.datPosition.find(item => item['id'] == value);
    }else if(group=='company'){
      obj = this.oplandb.datCompany.find(item => item['id'] == value);
    }
    if(obj){
      return obj['name'];
    }else{
      return "unknown"
    }
  }

  onItemClicked(e,item:any){
    // when a person's name was clicked on the navigation panel
    this.itemClicked.emit(item);
  }



  onSortClicked(e){

    //this.sortSelectionClicked.emit(this.navSortBy);
    this.udf.searchText='';

    this.udf.currentModule=e.srcElement.innerText;
    this.oplandb.setPersonnelNavGroup(e.srcElement.innerText)
    
    //alert('this.udf.currentModule:'+ this.udf.currentModule)
   

  }
  onSerchClicked(clear?:boolean){
    if(clear==undefined)clear=false;
    if(clear)this.udf.searchText="";
    this.oplandb.refreshNavGroup();
    //this.oplandb.setPersonnelNavGroup();
  }

  onSearchTextChange(){
    this.oplandb.refreshNavGroup();
    //this.oplandb.setPersonnelNavGroup();
  }


  ngOnInit() {
    let filterString:string="a"
    this.udf.searchText="";

    this.oplandb.getCompanies(true,data=>{
      this.oplandb.getPositions(true,data=>{
        this.oplandb.getPersonnel(false,data=>{
          this.oplandb.setPersonnelNavGroup()
        })
      })
    })

    //this.getPersonnelsRaw();
      
/*
    this.items[0]['actCount']=10;
    this.items[0]['nameGroup']='X';
    this.items[0]['lastName']='XYtel';

    this.items.sort(function(a,b) {return (a['lastName'] > b['lastName']) ? 1 : ((b['lastName'] > a['lastName']) ? -1 : 0);})
    
    this.itemsByLastName=this.groupBy(this.items,'nameGroup');
  */
    
  }


  groupExpCol(e){

    if(this.udf.modePersonnel!="normal")return;

    let iElem = $(e.srcElement);
    
    if(iElem.attr('class').indexOf('-down')!=-1){
      let lbl = iElem.parent().parent().find('label').html();
      this.udf.currGroup=lbl;

    }else{
      this.udf.currGroup="-";
    }


  }

  expandToggle(e){
    if(this.udf.modePersonnel!="normal")return;
    //expAll
    this.expAll=!this.expAll;
  }

  ngAfterContentInit(){
    //this.thisComponent.emit(this);
    
  }

  //var item = myArray.find(item => item.id === 2);

  isActivityModule():boolean{
    //this.udf
    return false;
  }

  getMonthName(mo):string{
    return this.monthNames[mo];
  }
  
  toggleActivityGroup(type:string,grp:any):void{

    let actg:Array<object>=this.oplandb.activityGroupings;
    let parent:any=grp.parent;

    if(type=='YEAR'){
      actg.forEach((yr:any)=>{
        if(yr.exp && yr.year!=grp.year)yr.exp=false;
      });
    }else if(type=='MONTH'){
      parent['months'].forEach((mo:any)=>{
        if(mo.exp && mo.month!=grp.month)mo.exp=false;
      });
    }else if(type=='PRIORITY'){
      parent['priorities'].forEach((pri:any)=>{
        if(pri.exp && pri.priority!=grp.priority)pri.exp=false;
      });
    }

    grp.exp=!grp.exp;

  }

}



