import { Directive, ViewContainerRef, TemplateRef, OnInit } from '@angular/core';

@Directive({
  selector: '[testDir]'
})
export class TestDirDirective implements OnInit {

  constructor(private viewContainer:ViewContainerRef, private template: TemplateRef<object>) { }

  data:Array<object>=[
    {id:1,name:"Item 1"},
    {id:2,name:"Item 2"},
    {id:3,name:"Item 3"},
    {id:4,name:"Item 4"},
  ];

  ngOnInit(){
    const condition=true;
    if(condition){
      this.viewContainer.createEmbeddedView(this.template);
    }else{
      this.viewContainer.clear();
    }
  }

}



