import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';

@Component({
  selector: 'app-test-window-freeze',
  templateUrl: './test-window-freeze.component.html',
  styleUrls: ['./test-window-freeze.component.css']
})
export class TestWindowFreezeComponent implements OnInit {

  rowHeaderWidth:number=200;
  colHeaderHeight:number=100;

  constructor() { }

  ngOnInit() {
  }

  onElementScroll(e){
    let cont=$(e.srcElement)
    //console.log(cont.scrollTop())
    //console.log(cont.scrollLeft())

    let ul=cont.find('[name="ul_pane"]');
    ul.css('top',cont.scrollTop());
    ul.css('left',cont.scrollLeft());

    let ur=cont.find('[name="ur_pane"]');
    ur.css('top',cont.scrollTop());

    let ll=cont.find('[name="ll_pane"]');
    ll.css('left',cont.scrollLeft());

  }    

}



