import { AfterContentInit, ViewChild, Component, 
         ViewContainerRef, ComponentFactoryResolver,
         OnInit, Input, Output, EventEmitter, 
         ElementRef, Renderer } from '@angular/core';

@Component({
  selector: 'app-pnl-vert-split',
  templateUrl: './pnl-vert-split.component.html',
  styleUrls: ['./pnl-vert-split.component.css']
})
export class PnlVertSplitComponent implements OnInit, AfterContentInit {

  leftStatus:string='none';
  rightStatus:string='none';

  maskDisplayStyle:string='inline';
  displayMask:boolean=false;
  splitHandleWidth:string="5px";
  splitHandleLeft:string="500px";

  @Input() leftComponent:any;
  @Input() rightComponent:any;
  @Output() leftComponentLoaded:any=new EventEmitter();

  @ViewChild('leftContainer', {read: ViewContainerRef}) leftContainer;

  constructor(private resolver:ComponentFactoryResolver,renderer:Renderer,elementRef:ElementRef) { }

  ngOnInit() {
  }

  ngAfterContentInit(){
    const leftContainerFactory = this.resolver.resolveComponentFactory(this.leftComponent.component);
    const data = this.leftComponent.data;
    const comp=this.leftContainer.createComponent(leftContainerFactory);

    let k:string;
    for(k in data){
      comp.instance[k] = data[k];
    }

    this.leftComponentLoaded.emit(comp.instance);

  } 
  
  

  onMouseDown(e){

    this.leftStatus = 'left mouse button pressed';
    this.displayMask=true;

  }

  onMouseUp(e){
    this.leftStatus = 'left mouse button released';
    
  }

  onMaskMouseUp(e){
    this.displayMask=false;
  }

  onMaskMouseMove(e){
    this.splitHandleLeft = (e.offsetX - parseFloat(this.splitHandleWidth)/2) + 'px';
  }

}



