import { Component, OnInit, Input,TemplateRef } from '@angular/core';
import * as $ from 'jquery';

@Component({
  selector: 'our-table',
  templateUrl: './our-table.component.html',
  styleUrls: ['./our-table.component.css']
})
export class OurTableComponent implements OnInit {

  @Input()
  scrollingHeader:TemplateRef<any>;

  @Input()
  frozenHeader:TemplateRef<any>;

  @Input()
  scrollingDetails:TemplateRef<any>;

  @Input()
  frozenDetails:TemplateRef<any>;

  isWithFrozen:boolean=false;

  dataRows:Array<object>=[
    {id:1, name:"Item 1",desc:"Item 1 description",url:'<a href="https://wwww.yahoo.com">Yahoo!!!</a>'},
    {id:2, name:"Item 2",desc:"Item 2 description",url:"https://wwww.google.com"},
    {id:3, name:"Item 3",desc:"Item 3 description",url:"https://wwww.bpi.com"},
    {id:4, name:"Item 4",desc:"Item 4 description",url:"https://wwww.i-rbi.com"},
    {id:5, name:"Item 5",desc:"Item 5 description",url:"https://wwww.soga.com"},
    {id:6, name:"Item 1",desc:"Item 1 description",url:'<a href="https://wwww.yahoo.com">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliq</a>'},
    {id:7, name:"Item 2",desc:"Item 2 description",url:"https://wwww.google.com"},
    {id:8, name:"Item 3",desc:"Item 3 description",url:"https://wwww.bpi.com"},
    {id:9, name:"Item 4",desc:"Item 4 description",url:"https://wwww.i-rbi.com"},
    {id:10, name:"Item 5",desc:"Item 5 description",url:"https://wwww.soga.com"},
  ];

  columnDefs:Array<OurTableColumnDef>=[
    {
      name:"col1",
      headerText:"ID",
      freeze:true,
      type:"string",
      dataField:"id",
      width:120,
      align:"center",
      visible:false
    }
       
    ,
    {
      name:"col2",
      headerText:"Name",
      freeze:false,
      type:"string",
      dataField:"name",
      sortable:true,
      sort:"asc",
      order:1,
      width:120,
      visible:true
    }

    ,
    {
      name:"col3",
      headerText:"Description",
      freeze:false,
      type:"string",
      dataField:"desc",
      sortable:true,
      sort:"asc",
      order:2,
      visible:true
    }

    ,
    {
      name:"col4",
      headerText:"Yes\/No",
      freeze:false,
      type:"string",
      dataField:"url",
      sortable:true,
      sort:"asc",
      order:3,
      width:200,
      visible:true,
      html:true
    }
  ];
  @Input() tableDef:object={
    columnDefs:this.columnDefs
  };


  constructor() { }

  //rowHeaderWidth:number=this.isWithFrozen ? 200 : 0;
  rowHeaderWidth:number=0;
  colHeaderHeight:number=68;
  scrollLeft:number=0;
  scrollTop:number=0;
  
  ngOnInit() {

    this.isWithFrozen = (this.frozenHeader != null && this.frozenDetails != null);
    this.rowHeaderWidth = this.isWithFrozen ? 200 : 0;
    setTimeout(()=>{this.setRowHeight();},0);
    
  }

  onScroll(e){
    let dv=$(e.srcElement)
    this.scrollLeft=dv.scrollLeft();
    this.scrollTop=dv.scrollTop();
  }

  setRowHeight(){
    let tbls=$('table');

    let LL_TR = $(tbls[2]).find('tr');
    let LR_TR = $(tbls[3]).find('tr');
    for(let i=0;i<LL_TR.length;i++){
      let ltr = $(LL_TR[i]);
      let rtr = $(LR_TR[i]);
      let ltrh = ltr.height();
      let rtrh = rtr.height();

      if(ltrh<rtrh){
        ltr.css('height',rtrh+'px');
      }else if(ltrh>rtrh){
        rtr.css('height',ltrh+'px');
      }

    }
  }
  
  getClass():object{
    return {
      "table":true,
      "table-sm":true,
      "table-hover":true,
      "table-bordered":true,
    }
  }
     
}

@Component({
  selector: 'our-table-header',
  template: `
  <ng-container *ngTemplateOutlet="inputTemplate"></ng-container>
  `,
  styleUrls: ['./our-table.component.css']
})
export class OurTableHeaderComponent implements OnInit {

  @Input()
  inputTemplate:TemplateRef<any>;

  constructor() { }

  ngOnInit() {

  }

}

@Component({
  selector: 'our-table-body',
  template: `
  <ng-container *ngTemplateOutlet="inputTemplate"></ng-container>
  `,
  styleUrls: ['./our-table.component.css']
})
export class OurTableBodyComponent implements OnInit {

  @Input()
  inputTemplate:TemplateRef<any>;

  constructor() { }

  ngOnInit() {
  }

}
export class OurTableColumnDef {
  constructor(){
    this.name="";
    this.freeze=false;
    this.type="string";
    this.readOnly=true;
    this.width=120;
    this.sort="";
    this.sortable=false;
    this.hasFilter=true;
    this.visible=true;
    this.order=0;
    this.align="";
    this.html=false;
  }

  name:string;
  headerText:string;
  dataField:string;

  freeze?:boolean;
  type?:string;
  readOnly?:boolean;
  width?:number;
  sort?:string;
  sortable?:boolean;
  hasFilter?:boolean;
  visible?:boolean;
  order?:number;
  align?:string;

  html?:boolean;
  

}


