  validateChange():boolean{

    let act:any=this.hovAct;

    // set newStart and/or newEnd to original activity startan
    if(this.hovNewStart.length==0) this.hovNewStart=act.sd;
    if(this.hovNewEnd.length==0) this.hovNewEnd=act.ed;    

    let sd:Date = G.stringToDate(act.sd);
    let ed:Date = G.stringToDate(act.ed);

    let mind:Date = G.stringToDate(act.minDate);
    let maxd:Date = G.stringToDate(act.maxDate);

    let nsd:Date = G.stringToDate(this.hovNewStart);
    let ned:Date = G.stringToDate(this.hovNewEnd);

    let diffOld:number = G.daysDiff(sd,ed);         // duration between the old start and end dates
    let diffNew:number = G.daysDiff(nsd,ned);       // duration between the new start and end dates
    let diffStartMin:number = G.daysDiff(sd,mind);  // days from activity start to earliest member assignment
    let diffMinMax:number = G.daysDiff(mind,maxd);  // days between the minimum and maximum assignment dates

    if(diffOld != diffNew && !act.block){
      // if activity duration is changed, not a block activity, trigger validation
      // to make sure all members will still fit inside the new duration
      
      // required duration for the activity to be able to accommodate all members
      let reqDuration:number = diffStartMin + diffMinMax;

      // new duration new date span less take off offset
      let newDuration:number = diffNew;

      //this.debugText +=  ", reqDuration:" + reqDuration + ", newDuration:" + newDuration

      // if newDuration is less than the required duration, changes made will not be allowed
      if(newDuration < reqDuration)
        return false;  // new duration will not be able to accommodate all member assinments

    }    
    return true;
  }

