import { Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import * as $ from 'jquery';

@Component({
  selector: 'app-ctl-drop-down',
  templateUrl: './ctl-drop-down.component.html',
  styleUrls: ['./ctl-drop-down.component.css']
})
export class CtlDropDownComponent implements OnInit {

  itemId:number=0;

  @Input()
  get drpdValue() {
    return this.itemId;
  }

  @Output() drpdValueChange :  EventEmitter<number>;  // event emiter must be named "<propertyname>drpdValueChange"
  set drpdValue(val) {  // in this case property name is drpdValue therefore the emitter name is drpdValueChange
    this.itemId = val;
    //console.log('value:'+val);
    this.drpdValueChange.emit(this.itemId);
  }
  
  @Output() itemSelected:any = new EventEmitter();

  @Input() drpdData:Array<object>=[];
  
  @Input() drpdWidth:number=200;
  @Input() drpdStartRow:number=0;
  @Input() drpdRowHeight:number=25;
  @Input() drpdDispRows:number=20;
  @Input() drpdFontSize:string='0.8em';

  drpdFilter:string="";

  drpdRecCount:number=this.drpdData.length;
  drpdHeight:number=this.drpdDispRows * this.drpdRowHeight;
  drpdScrollHeight:number = Math.max(this.drpdHeight, this.drpdRecCount * this.drpdRowHeight-1);

  drpdDispData:Array<object> = []; 

  constructor() { 
    this.drpdValueChange=new EventEmitter();
  }

  ngOnChanges(){
    // alert('changed!!!')
    // if bound data is changed coded in this hook will be executed
    //if(this.drpdValue)
    this.setInitialIndex()

  }

  setInitialIndex(){
    return;
    /*let idx=this.drpdData.findIndex((item:any)=>{ return item.id==this.drpdValue});
    if(idx==-1){
      this.drpdStartRow = 0;
    }else{
      this.drpdStartRow = idx;
    }
    this.onScroll(null);*/
  }

  ngOnInit() {
/*
    this.drpdRecCount=this.drpdData.length;
    this.drpdHeight=this.drpdDispRows * this.drpdRowHeight;
    this.drpdScrollHeight = Math.max(this.drpdHeight, this.drpdRecCount * this.drpdRowHeight-1);
    this.drpdDispData = this.drpdData.slice(this.drpdStartRow,this.drpdStartRow+this.drpdDispRows+1); 
*/
    
    setTimeout(()=>{
        this.drpdRecCount=this.drpdData.length;
        this.drpdHeight=this.drpdDispRows * this.drpdRowHeight;
        this.drpdScrollHeight = Math.max(this.drpdHeight, this.drpdRecCount * this.drpdRowHeight-1);
        this.drpdDispData = this.drpdData.slice(this.drpdStartRow,this.drpdStartRow+this.drpdDispRows+1); 
        this.setInitialIndex();
    },500)
    
  }

  drpdScrollContainerHeight():number{
    return ((Math.min(this.drpdDispRows,this.drpdData.length)) * this.drpdRowHeight);
  }


  onScroll(evt){
    let det=$('#drpdInformationDiv');
    let oldTop:number = det.position().top;
    let newTop:number = evt.target.scrollTop;

    if(newTop>(oldTop+this.drpdRowHeight) || ((oldTop-newTop+this.drpdRowHeight)>=this.drpdRowHeight)){
      det.css('top',(newTop) + 'px');
      this.drpdStartRow = parseInt(String(newTop/this.drpdRowHeight));
      this.drpdDispData=this.drpdData.slice(this.drpdStartRow,this.drpdStartRow+this.drpdDispRows+1);
    }

  }

  itemClick(item:any){
    //this.drpdValue=item.id;
    //this.itemValue.emit(item.id);
    //console.log(item);
    //this.itemSelected.emit(item.id);
    //alert('itemClick: '+ item.id);
  }


  itemMouseDown(item:any){
    this.drpdValue=item.id;
    //alert('itemMouseDown: '+ item.id);
  }
  


}



