import { Component, OnInit, Input } from '@angular/core';
import { AlvTableTemplateHeaderComponent } from '../alv-table-template-header/alv-table-template-header.component'
import * as $ from 'jquery';


@Component({
  selector: 'app-alv-table-template',
  templateUrl: './alv-table-template.component.html',
  styleUrls: ['./alv-table-template.component.css']
})
export class AlvTableTemplateComponent implements OnInit {

  @Input() options:any = {};
  @Input() data:any = {};

  @Input() width:number = 600;
  @Input() height:number = 300;

  //@Input() rowWidth:number = 0;

  tableDefs:Array<any>=[];
  tblGuid:string = this.uuidv4();
  tblId:string = "alv_tbl_" + this.tblGuid;
  tblTheadId:string = this.tblId + '_head';
  tblTbodyId:string = this.tblId + '_body';
  tblRowsId:string = this.tblId + '_rows';

  tblColumns:Array<AlvTableTemplateHeaderComponent>=[];

  constructor() { }

  ngOnInit() {
    if(this.options.tableDefs){
      this.tableDefs  = this.options.tableDefs;
    }else{
      this.tableDefs  = [];
    }

    this.options.startRow = 0;
    this.options.endRow = 1;

    setTimeout(()=>{this.setupTable();},10);

  }

  columnWidth(idx:number):number{

    let dc:AlvTableTemplateHeaderComponent=this.tblColumns[idx];

    //console.log('width:' +  dc.width);


    return dc.width;
  }

  rowWidth():number{
    let ret:number=0;

    this.options.colDefs.forEach(e=>{
      if(!e.hidden){
        ret+=e.width;
      }
    })

    return ret;
  }


  tableWidth():number{
    return this.width;
  }

  tableHeight():number{
    return this.height;
  }

  scrollTimeout:any = 0;
  tblScroll(e){
    let cont:any=$(e.target);
    let head:any=cont.parent().find('thead');
    head.scrollLeft(cont.scrollLeft());

    let tbody:any = $('#'+this.tblTbodyId)
    this.tblScrollTop = tbody.scrollTop();


    //let offFullRec:number = Number(parseInt(String(this.tblScrollTop / this.tblRowHeight)));
    

    //this.options.startRow  = parseInt(String(this.tblScrollTop / this.tblRowHeight));
    //this.options.endRow  = Number(this.options.startRow) + Number(this.tblMaxRecords);    

    this.tblRecOff2 = this.tblScrollTop / this.tblRowHeight;

    let mt:number = this.tblRowHeight * parseInt(String(this.tblScrollTop / this.tblRowHeight));

    this.tblRecOff = (this.tblRecOff2 - parseInt(String(this.tblRecOff2))) * this.tblRowHeight;

    mt = this.tblScrollTop - this.tblRecOff;

    console.log()

    let trs:any=$(tbody.find('tr'));
    //$(trs[0]).css('margin-top',
    //  Math.min(this.tblScrollTop,this.tblRowHeight * (this.tblTotalRecords - this.tblMaxRecords))  + 'px')

    if(this.scrollTimeout)clearTimeout(this.scrollTimeout) 
    this.scrollTimeout = setTimeout(()=>{

      let newRecStart:number =  parseInt(String(this.tblRecOff2));
      if(this.options.startRow!=newRecStart){
        this.options.startRow = newRecStart;
        this.options.endRow = Math.min(newRecStart + this.tblMaxRecords,this.tblTotalRecords);
      }

      let rowsDiv:any = $('#'+this.tblRowsId);
      if(rowsDiv.length){
        rowsDiv.css('padding-top',
          Math.min(this.tblScrollTop - this.tblRecOff,this.tblRowHeight * (this.tblTotalRecords - this.tblMaxRecords + 1))  + 'px');
      }

    },5); // delay scroll updates by 5ms


  }

  calcRowIndex(){
    console.log('calcRowIndex....')
    this.options.endRow = Number(this.options.startRow) +  Number(this.tblMaxRecords);
  }

  testClick(e){

    this.options.colDefs.forEach(e=>{
      e.order = e.index;
      //console.log(e);
    });

    this.setupTable();

  }

  tblRowHeight:number = 0;
  tblMaxRecords:number = 0;
  tblTotalRecords:number = 0;

  tblScrollTop:number = 0;
  tblRecOff:number = 0;
  tblRecOff2:number = 0;


  setupTable(){

    let tbl:any = $('#'+this.tblId);
    let tbody:any = $('#'+this.tblTbodyId);
    let thead:any = $('#'+this.tblTheadId);

    let htrs:any = thead.find('tr');
    let trs:any = tbody.find('tr');
    
    this.tblRowHeight = trs.height();
    this.tblMaxRecords = parseInt(String(tbody.height()/this.tblRowHeight)) + 2;
    if(this.data.rows){
      this.tblTotalRecords = this.data.rows.length;
    }else{
      this.tblTotalRecords = this.data.length;
    }

    this.options.startRow  = 0;
    this.options.endRow  = this.options.startRow + this.tblMaxRecords;

    let rowsDiv:any = tbl.find('#'+this.tblRowsId);
    if(rowsDiv.length){
      rowsDiv.css('height',(this.tblTotalRecords * this.tblRowHeight) + 'px');
    }

    setTimeout(()=>{
      //htrs = thead.find('tr');
      //trs = tbody.find('tr');
      
      //htrs.addClass('d-flex').css('position','relative').css('width',this.rowWidth + 'px');
      //trs.addClass('d-flex').css('width',this.rowWidth + 'px');
    },50);

  }

  uuidv4() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
      var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
      return v.toString(16);
    });
  }


}



