import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { Chart } from 'chart.js';

import  { AppUDFService  } from '../../app.udfs';
import { AppOplpanDataService } from '../../app.oplandata';

import { ISubscription } from  'rxjs/Subscription';

import * as $ from 'jquery';
import * as G from  '../../app.globals';
import * as U from  '../../app.udfs';


@Component({
  selector: 'app-pobprofile',
  templateUrl: './pobprofile.component.html',
  styleUrls: ['./pobprofile.component.css']
})
export class PobprofileComponent implements OnInit {

  @ViewChild('cnv') cnv:ElementRef;

  chartColors=["#3c6494","#963d3b","#799244","#634d7e","#39869b","#c27535", "#4978b1","#46a1b9","#e78c41","#7e9bc8","#ca7e7d","#aec683","#9b89b3","#7cbbcf","#f8aa79","#ff0000","#c00000","#745995"];
  myChart =null;
  chart = []; // This will hold our chart info
  chartData:object = {datasets:[]};
  profileData={"message":"","cod":"200","city_id":2643743,"calctime":0.0875,"cnt":3,"list":[{"main":{"temp":279.946,"temp_min":100.946,"temp_max":279.946,"pressure":1016.76,"sea_level":1024.45,"grnd_level":1016.76,"humidity":100},"wind":{"speed":4.59,"deg":163.001},"clouds":{"all":92},"weather":[{"id":500,"main":"Rain","description":"light rain","icon":"10n"}],"rain":{"3h":2.69},"dt":1485717216},{"main":{"temp":282.597,"temp_min":282.597,"temp_max":282.597,"pressure":1012.12,"sea_level":1019.71,"grnd_level":1012.12,"humidity":98},"wind":{"speed":4.04,"deg":226},"clouds":{"all":92},"weather":[{"id":500,"main":"Rain","description":"light rain","icon":"10n"}],"rain":{"3h":0.405},"dt":1485745061},{"main":{"temp":279.38,"pressure":1011,"humidity":93,"temp_min":278.15,"temp_max":280.15},"wind":{"speed":2.6,"deg":30},"clouds":{"all":90},"weather":[{"id":701,"main":"Mist","description":"mist","icon":"50d"},{"id":741,"main":"Fog","description":"fog","icon":"50d"}],"dt":1485768552}]}
  statusText:string="";
  link=null;


  constructor(public udf:AppUDFService,
    public oplandb:AppOplpanDataService) { }

  ngOnInit() {

    this.udf.resetPOBProfileScope();

    this.reloadPOBProfile();

  }

  ngOnDestroy(){
    this.oplandb.clearSubscription(this.subsReload);
  }

  subsReload:ISubscription;
  reloadPOBProfile(){

    this.udf.profStartDate=G.longToDate(this.udf.profDispStartDate);
    this.udf.profEndDate=G.longToDate(this.udf.profDispEndDate);

    this.oplandb.processData({
      method:"getPOBProfile",
      processMessage:"Extracting POB Profile data. Please wait...",
      subscription:this.subsReload,
      data:{
        startDate:G.dateToString(this.udf.profStartDate),
        endDate:G.dateToString(this.udf.profEndDate)
      },
      onSuccess:data=>{

        this.chartData = {datasets:[]};

        let teams = data['teams'];

        this.chartData['labels']=[];

        if(data['d0']!=undefined){

          let stdLimit=[];
          let upLimit=[];

          let sd:Date=this.udf.profStartDate;

          for(let idx=1; idx <= data['d0'].length; idx++){

            let dt=new Date(sd.getFullYear(),sd.getMonth(),sd.getDate()+idx-1);
            let lbl=dt.toLocaleDateString('en-GB',  {year: '2-digit', month: 'short', day: 'numeric' });

            this.chartData['labels'].push(lbl.replace(/ /gi,'-'));
            let std:number = data['std'][idx-1];
            let upm:number = data['upm'][idx-1];
            stdLimit.push(std);  // standard
            upLimit.push(Math.max(upm - std, 0));   // upmanning less standard
          }

          for(let idx=0; idx < teams.length; idx++){

            // split team info to separate team name from the switches that come with it
            // (ie. tean field indicator, sort field, ..., etc)
            let tmArr:Array<string>=teams[idx].split('^')

            this.chartData['datasets'].push(
              {
                label:tmArr[tmArr.length-1], // teams[idx], //.substr(3),
                data: data["d"+idx],
                backgroundColor: this.chartColors[idx]
              }

            );
          } // for ...

          this.chartData['datasets'].push(
            {
              type:'line',
              label:"STD POB Limit",
              //display:'linear',
              data:stdLimit,
              fill:false,
              lineTension: 0,

              borderColor:"rgba(255,0,0,0.7)",
              backgroundColor:'white',
              borderWidth:'3',
              pointRadius:0

            }
          );

          this.chartData['datasets'].push(
            {
              type:'line',
              label:"UpManning Limit",
              data:upLimit,
              fill:false,
              lineTension: 0,

              borderColor:"rgba(255,0,0,0.9)",
              backgroundColor:'white',
              borderWidth:'3',
              pointRadius:0
            }
          );

          setTimeout(()=>{this.generateProfile()},100);

        }else{
          // no data found
          this.statusText="Sorry no data found within the selected date scope !";
        }


        /**
         *
         *
         this.config['stdPOBLimit']['limits']=[];
    this.config['upmPOBLimit']['limits']=[];
    this.config['mobLimit']['limits']=[];
    this.config['demobLimit']['limits']=[];
    this.config['upmanPeriods']['periods']=[];

         console.log("Configuration data...")

         console.log(this.oplandb.config)
         setTimeout(()=>{
          console.log(this.oplandb.config.stdPOBLimit.limits)
          console.log(this.oplandb.config.upmPOBLimit.limits)
          console.log(this.oplandb.config.upmanPeriods.periods)
          console.log(this.oplandb.config.upmanPeriods.periods)

         },2000)
         */



      } // on success 2018-09-27
    }) // process data
  }

  downloadProfile(){

    if(!this.cnv){
      this.statusText="Cannot save empty chart !";
      return;
    }

    let canvas=this.cnv.nativeElement;

    if(this.link==null){

      this.link = document.createElement('a');
      this.link.id="downloadProfile"
      this.link.innerHTML = 'download image';
      this.link.addEventListener('click', function(ev) {
        this.link.href = canvas.toDataURL();
        this.link.download = "mypainting.png";

      },false);
      document.body.appendChild(this.link);

    }

    $("#downloadProfile").trigger("click");

  }

  generateProfile(){

    if(this.myChart!=null){
      this.myChart.destroy();
    }

    this.myChart = new Chart('canvas', {
      type: 'bar',
      title: {
        text:"SWP POB Profile"
      },
      data:  this.chartData ,
      options: {
        title:{
          display:true,
          text:this.getCardTitle(),
          fontSize:18,
          position:'bottom'
        },
        legend:{
          display:true,
          position:'bottom'
        },
        scales: {
          xAxes: [{ stacked: true }],
          yAxes: [{ stacked: true }],
          maintainAspectRatio:false,
          responsive:true
        }
      }
    });


  }

  getCardTitle():string{
    let sd:Date=this.udf.profStartDate;
    let ed:Date=this.udf.profEndDate;

    return "SWP POB Profile ("+ this.udf.profDispStartDate +" to " + this.udf.profDispEndDate + ")";
  }

  disableReload():boolean{
    // check if no change on the current filter scope
    let sd:Date=G.longToDate(this.udf.profDispStartDate)
    let ed:Date=G.longToDate(this.udf.profDispEndDate)

    let retVal:boolean=((this.udf.profDispStartDate == G.dateToString(this.udf.profStartDate,true)) &&
      (this.udf.profDispEndDate == G.dateToString(this.udf.profEndDate,true)));

    this.statusText='';

    if(!retVal){
      // check if invalid date
      retVal = (ed < sd);
      if(retVal)this.statusText='( Start date cannot be later than end date )'
    }else{
      // date was changed
      this.statusText='';
    }

    if(!retVal){
      // check maximum dates
      retVal = (G.daysDiff(sd,ed) > this.udf.profScopeLimit);
      if(retVal)this.statusText='( Scope cannot be more than '+ this.udf.profScopeLimit + ' days )'
    }

    //alert(ed-sd);

    //this.statusText=String(G.daysDiff(sd,ed) + ' , ' + this.udf.calScopeLimit);


    return retVal;
  }



}



