  getCount(type:string,idx?:any,compWith?:Array<any>):any{
    let cfg:any = this.oplandb.config;
    if(!cfg.arranged)return "";
    let ctr=0,i;
    switch(type){
      case "FEMALE":
        let fArr=this.totalFemale.filter((e)=>{return (e % 2)!=0})
        return fArr.length ;
      case "POBLIMIT":
        return cfg['actualPOBValues'][idx];

      case "ISOVERPOB":
        ctr=0;
        for(i=0;i<cfg['actualPOBValues'].length;i++)
          if(cfg['actualPOBValues'][i] < compWith[i])ctr++;

        return ctr;

      case "NIGHTLIMIT":


   

        // initial limit value is the actual POB Limit less the standard limit
        // this will only have a value greater than 0 if the upmanning is enforced
        
        /*
          2018-02-09 - modified overnight limits to use total 
            offshore pob instead of the upmanning limit as the higher reference value
        */ 
        
        //let ret:number = cfg['actualPOBValues'][idx]-cfg['stdPOBValues'][idx];
        let ret:number = cfg['actualPOBValues'][idx]>cfg['stdPOBValues'][idx] ? this.totalOffshorePOB[idx]-cfg['stdPOBValues'][idx] : 0;
        //let ret:number =  cfg['actualPOBValues'][idx]-cfg['stdPOBValues'][idx];
        
        /*if(ret==0){
          let availBeds:number= this.getCount('POBLIMIT',idx) - this.totalOffshorePOB[idx];
          if(availBeds>=0)ret=999;
        }*/
        

        return ret;

      case "MOBLIMIT":
        return cfg['mobValues'][idx];

      case "ISOVERMOB":
        ctr=0;
        for(i=0;i<cfg['mobValues'].length;i++)
          if(cfg['mobValues'][i] < compWith[i])ctr++;

        return ctr;

      case "DEMOBLIMIT":
        return cfg['demobValues'][idx];

      case "ISOVERDEMOB":
        ctr=0;
        for(i=0;i<cfg['demobValues'].length;i++)
          if(cfg['demobValues'][i] < compWith[i])ctr++;

        return ctr;
    
      case "ISOVERNIGHT":
        ctr=0;
        for(i=0;i<cfg['actualPOBValues'].length;i++){
          /*
            2018-02-09 - modified overnight limits to use total 
              offshore pob instead of the upmanning limit as the higher reference value
          */ 
          // let limit:number = cfg['actualPOBValues'][i]-cfg['stdPOBValues'][i];
          let limit:number = this.totalOffshorePOB[i]-cfg['stdPOBValues'][i];
          if(limit == 0){
            let availBeds:number= cfg['actualPOBValues'][i] - this.totalOffshorePOB[i];
            if(availBeds>=0){
              // arbitrary number that will make sure that the actual value to be compared is lower
              limit=999; 
            }
          }else{
            if(limit > compWith[i])ctr++; // if overnight crew is not filling up the slots for night shift
          }
          
        }
          
        return ctr;

      default:
    }
    return 0;
  }

  pInfo(person:object):object{
    //return this.datPersonnels[index];

    
    
    if(person['chartInfo']==undefined){

      // define and initialize all elements to null chart information
      person['chartInfo']=[];
      this.indexArray.forEach(element => {
        person['chartInfo'].push(1);
      });

      
      person['activities'].forEach(element => {

        // convert activity dates to date type  
        let sd:Date=G.stringToDate(element['start']);
        let ed:Date=G.stringToDate(element['end']);

        // store start date, end date and days to activity object
        element['startDate']=sd;
        element['endDate']=ed;
        element['rectSpan']=this.dateDiff(sd,ed) + 1;
      });


    }

    return person;

  }

