  getWidthValues(arr:Array<any>,reference:number){

    let i:number;
    let curVal:number;
    let iCurrentValue:number=0;
    let iTotalAbsolute:number=0;
    let iRemainingWidth:number=0;
    let iValStr:string;
    let withPercent:boolean=false;

    // get total absolute value
    for(i=0;i<arr.length;i++){
      iValStr=String(arr[i]);
      if(iValStr.indexOf('%')==-1){
        // absolute value
        iTotalAbsolute+=parseFloat(iValStr);
      }else{
        withPercent=true;
      }
    }

    if(withPercent){
      iRemainingWidth = reference - iTotalAbsolute;
      for(i=0;i<arr.length;i++){
        iValStr=String(arr[i]);
        if(iValStr.indexOf('%')!=-1){
          arr[i] = iRemainingWidth * parseFloat(iValStr)/100;
          }
        }
    }

    for(i=1;i<arr.length;i++){
      arr[i]=parseFloat(arr[i])+parseFloat(arr[i-1])    
    }

  }


