  processData(data:object){
    let datAct:Array<object>=[];
    let datCoy:Array<object>=[];
    let datTms:Array<object>=[];
    let datPos:Array<object>=[];
    let datPer:Array<object>=[];

    // process activities data
    data['activities'].forEach(elem=> {
      datAct[datAct.length]={"id":elem[0], "name":elem[1], "start":elem[2], "end":elem[3]};
    })

    // process companies data
    data['companies'].forEach(elem=> {
      datCoy[datCoy.length]={"id":elem[0], "name":elem[1]};
    })

    // process positions data
    data['positions'].forEach(elem=> {
      datPos[datPos.length]={"id":elem[0], "name":elem[1]};
    })


    // process teams data
    //sample data format - {id:1,name:"Operations Core Crew",isCore:true,order:1}

    
    let isFirst:boolean=true;
    data['teams'].forEach(elem=> {

      let exp=localStorage.getItem("texp"+elem[0]);
      if(exp==undefined)exp="0";

      datTms[datTms.length]={
        "id":elem[0], 
        "name":elem[1], 
        "isCore":(elem[2]==1),
        "order":elem[3],
        "isUpManning":(elem[4]==1),
        "expChart":(exp=="1")
        //"expChart":isFirst

      };
      isFirst=false;
    })

    
    // process bed and upmanning limits
    // ":[["",78],["20170419",70],["    

    
    this.maxBedsDates=[];
    this.upManningDates=[];

    data['maxBeds'].forEach(elem=>{
      this.maxBedsDates[this.maxBedsDates.length]={
        "date": (elem[0].length ? elem[0] : null),
        "limit": elem[1]
      }
    });

    data['upManning'].forEach(elem=>{
      this.upManningDates[this.upManningDates.length]={
        "date": (elem[0].length ? elem[0] : null),
        "limit": elem[1]
      }
    });    


    // process personnels data
    /*sample data format - 
    
    {
      id:1,
      name:"Bonifacio Lapuz",
      team:1,
      order:1,
      coy:1,
      pos:6,
      activities:[
        {id:1, start:"20170323", end:"20170408", mob:true, demob:true,coy:1,pos:6,team:1,isNight:true},
        {id:2, start:"20170426", end:"20170511", mob:true, demob:true,coy:1,pos:6,team:1,isNight:true}
      ]
    },

    */
    data['personnels'].forEach(elem=> {
      if(elem!=null){

        let acts:Array<object>=[]
        let posObj:any=datPos.find((item:any)=>{return item.id==elem[5]})
        let posName:string=elem[5];
        if(posObj)posName=posObj.name;

        datPer[datPer.length]={"id":elem[0], "name":elem[1], 
                              "team":elem[2], "order":elem[3], 
                              "coy":elem[4], "pos":elem[5], "grp":elem[6], "gender":elem[9],
                              "activities":acts,"posName":(posName+"                                                                                                       ").substr(0,50)};
  
                              elem[7].forEach(act=>{
                                acts[acts.length]={
                                    "id":act[0],
                                    "start":act[1],
                                    "end":act[2],
                                    "mob":(act[3]==1),
                                    "demob":(act[4]==1),
                                    "coy":act[5],
                                    "pos":act[6],
                                    "team":act[7],
                                    "isNight":(act[8]==1),
                                    "noPOB":(act[9]==1),
                                    "grpID":act[10],
                                    "isDay":act[11],
                                    "acpID":act[12]
                                }
                              });
      }
        
    })


    this.datActivities=datAct;
    this.datCompanies=datCoy;
    this.datPositions=datPos;
    this.datTeams=datTms;
    this.datPersonnels=datPer;

  }

