  disableReloadX():boolean{
    // check if no change on the current filter scope
    let sd:Date=G.longToDate(this.udf.calDispStartDate)
    //let ed:Date=G.longToDate(this.udf.calDispEndDate)
    return true;
  }

  disableReload():boolean{
    // check if no change on the current filter scope
    let sd:Date=G.longToDate(this.udf.calDispStartDate)
    let ed:Date=G.longToDate(this.udf.calDispEndDate)
    
    let retVal:boolean=((this.udf.calDispStartDate == G.dateToString(this.udf.calStartDate,true)) &&
      (this.udf.calDispEndDate == G.dateToString(this.udf.calEndDate,true)));

    this.statusText='';

    if(!retVal){
      // check if invalid date
      retVal = (ed < sd);
      if(retVal)this.statusText='( Start date cannot be later than end date )'
    }else{
      // date was changed
      this.statusText='';
    }

    if(!retVal){
      // check maximum dates
      retVal = (G.daysDiff(sd,ed) > this.udf.calScopeLimit);
      if(retVal)this.statusText='( Scope cannot be more than '+ this.udf.calScopeLimit + ' days )'
    }

    //alert(ed-sd);

    //this.statusText=String(G.daysDiff(sd,ed) + ' , ' + this.udf.calScopeLimit);


    return retVal;
  }

