  clkExpColTeam(teamID:any){
    let team:any=this.lkpTeams["TEAM_" + String(teamID)];
    team.expChart = !team.expChart;
    localStorage.setItem("texp"+teamID,team.expChart ? "1" : "0");
    this.setTeamRowPosition();
  }

  setTeamRowPosition(){

    let teamObj:any;

    let currentRowIndex:number=0;
    this.datPersonnelsCoreNonCore.forEach((core:any) => {

      // iterate through core group

      if(core.isIncluded){

        core['rowIndex']=currentRowIndex;  // assign row index for the core/noncore
        currentRowIndex++;  // increment index for the initial team

        core['data'].forEach((team:any) => {
          // iterate through teams

          if(team.isIncluded){

            teamObj=this.lkpTeams['TEAM_'+ team.type];

            // assign row index for the team
            team['rowIndex']=currentRowIndex;  

            // add all personnels for the current team, then increment by 1
            if(teamObj.expChart){
              // determine number of groups in the current team
              currentRowIndex += team['data'].filter((per:any)=>{return per.isIncluded}).length + 
                                this.groupCount(team) + 1;  
            }else{
              currentRowIndex ++;  
            }

          }

          this.dataRows=currentRowIndex;
          
        }); // for each team in a core group
        
      }
    
    });  // for each core group
    

  }

  isNewGroup(team, person, idxPerson):boolean{
    let ret:boolean=false;
    let grpArr:Array<string>=person.grp.split(':');
    if(idxPerson==0 && grpArr[1]!="0"){
      ret = true;
    }else if(idxPerson!=0){
      let dataArray:Array<object>=team.data.filter((per:any)=>{return per.isIncluded})
      ret = (person.grp != dataArray[idxPerson-1]['grp']);
    }
    return ret;
  }

  isTeamExp(teamID:any):boolean{
    let team:any=this.lkpTeams["TEAM_" + String(teamID)];
    return team.expChart;
  }  

  groupRowIndex(team,grp):number{
    let grpKey:string = grp.replace(':','_')+'_rowIndex';
    let ret = team[grpKey];
    if(ret==undefined)ret = 0;
    ret += team.rowIndex;
    return ret;
  }

  groupText(grp):string{
    let ret:string="";
    let grpArr:Array<string>=grp.split(':');

    if(grpArr[0]=="A"){
      // activity for non-core teams
      // this.lkpActivities["ACT_" + String(element['id'])]=element;
      let actObj:any=this.lkpActivities["ACT_" + grpArr[1]];
      if(actObj==undefined){
        ret = "-";
      }else{
        ret = actObj.name;
      }
      
    }else{  
      // team group for core teams
      let grpObj:any=this.oplandb.config['idxTeamGroups']['r'+grpArr[1]];
      if(grpObj==undefined){
        ret="-";
      }else{
        ret=grpObj.name;
      }

    }

    return ret;
  }

  groupCount(team):number{
    // returns the count of group per team and set the group rowIndex under the team
    let ret=0;
    let prvGrp="-"
    let currRow = 0;

    //team.data.forEach((per:any)=>{
    this.getTeamActivityMembers(team).forEach((per:any)=>{
      
      // count groups where personnel isIncluded flag is set
//      if(per.isIncluded){

        let grpArr=per.grp.split(':');
        if(grpArr[1]!='0'){
          if(prvGrp!=per.grp){
            ret++;
            
            team[per.grp.replace(':','_') + '_rowIndex']=ret;
  
            prvGrp=per.grp;
          }
        }
        currRow++;      
//      }

    });
    return ret;
  }

  flagTitle(mode,compArr?:Array<any>):string{
    let ret:string='';
    let cnt:any = this.getCount(mode,0,compArr);

    if(cnt!=0){
      switch(mode){
        case "FEMALE":
          ret = "There "+ (cnt==1 ? "is" : "are") +" "+ cnt +" date"+(cnt==1 ? "":"s") +" where the female crew count is not an even number";
          break;
        case "ISOVERMOB":
          ret = "There "+ (cnt==1 ? "is" : "are") +" "+ cnt +" date"+(cnt==1 ? "":"s") +" where the number mobilization exceeded the limit";
          break;
        case "ISOVERDEMOB":
          ret = "There "+ (cnt==1 ? "is" : "are") +" "+ cnt +" date"+(cnt==1 ? "":"s") +" where the number demobilization exceeded the limit";
          break;        
        case "ISOVERNIGHT":
          ret = "There "+ (cnt==1 ? "is" : "are") +" "+ cnt +" date"+(cnt==1 ? "":"s") +" where the overnight slots not filled up";
          break;
        case "ISOVERPOB":
          ret = "There "+ (cnt==1 ? "is" : "are") +" "+ cnt +" date"+(cnt==1 ? "":"s") +" where the number of POB's exceeded the limit";
          break;
      }
    }
    return ret;
    

  }


