  moveDragHandle(e:any,act:any){
    if(!act || !e)return;

    let dragHandle:any = $('#dragHandle');
    act = $(act);

    // get x,y position of the hovered activity
    let pos:any = act.position();  

    // get relative position of the cursor, relative to the upper left corner of the hovered activity bar
    // <current mouse position - x,y of the activity's upper left corner
    if(e.type=='mousedown' || e.type=='mouseenter'){
      // make dragHandle width the same as the activity width
      //!dragHandle.css('width',act.width());
      dragHandle.css('width',act.attr('width'));

      // set offset values
      this.dragOffX = e.x - pos.left;
      this.dragOffY = e.y - pos.top;

    }

    // reposition handle
    dragHandle.css('top',(e.y - this.dragOffY  + this.dragScrollY - this.udf.bannerHeight) +'px');
    dragHandle.css('left',(e.x - this.dragOffX + this.dragScrollX) +'px');    

  }

  
  dragHandlePosition(mode:string,offset?:number):number{
    if(offset==undefined)offset=0;
    let hObj:any = $("#dragHandle");
    if(hObj){
      return hObj.position()[mode] + offset;
    }else{
      return 0;
    }
  }

  //getActMemDetails(actID,mem)
  getHovMemDetails(returnActivity?:boolean):any{
    if(!this.hovObjSrc) return null;

    if(returnActivity==undefined)returnActivity=false;

    let hovered:any=$(this.hovObjSrc);

    let idArr:Array<string>=hovered.attr('id').split('_');
    let act:dataActivity = this.oplandb.datActivities.find((e:any)=>{
      return e.id==idArr[2];
    })

    if(returnActivity) return act;

    let mem:any = act.members.find((e:any)=>{
      return e.id==idArr[3];
    });

    if(mem){
      let ids:string=""
      this.datPersonnels.forEach((p:any)=>{
        ids+=p.id +'('+ p.activities.length +'), '
      })

      let perArr:any = this.datPersonnels.filter((p:any)=>{
        return p.id==mem.perID;
      });


      let memDet:any=null;
      for(let perIdx=0;perIdx<perArr.length;perIdx++){
        memDet = perArr[perIdx]['activities'].find((acp:any)=>{
          return acp.acpID == idArr[3];
        })
        if(memDet)break;
      }

      return memDet;

    }else{
      return null;
    }



  }

  undoMove(memDet?:any,noArrange?:boolean){
    if(noArrange==undefined) noArrange=false;
    if(memDet==undefined) memDet =this.getHovMemDetails();
    if(memDet){

      if(memDet['sdBackup']){
        memDet['start'] = memDet['sdBackup'];
        memDet['end'] = memDet['edBackup'];

        memDet['sdBackup']=null;
        memDet['edBackup']=null;
        memDet['idBackup']=null;
      }

      this.hovObj=null;
      this.hovObjSrc=null;

      if(!noArrange) this.arrangeData();
    }
  }

  noMove:boolean=false;
